<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <komponen-spinner v-if="loading" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="row">
        <div class="card p-2 position-sticky" style="top: 60px; z-index: 2">
          <komponen-google-lokasi :reload-lokasi="randomString" @update-form-lokasi="updateFormLokasi" @data-profil="dataProfil" />
          <div class="d-flex justify-content-between align-items-end pt-2 overflow-auto">
            <div class="btn-group" role="group" aria-label="Basic outlined example" style="margin-top: -10px; min-width: 900px">
              <button type="button" class="btn btn-outline-secondary">Bonus : Rp. {{ dataProfilById.nominal_bonus }} / Bulan</button>
              <button type="button" class="btn btn-outline-secondary">Mulai : {{ statis.jam_start }}</button>
              <button type="button" class="btn btn-outline-secondary">Selesai : {{ statis.jam_end }}</button>
              <button type="button" class="btn btn-outline-secondary">Waktu : {{ currentTime }}</button>
              <button type="button" @click="refreshLokasi()" class="btn btn-outline-secondary">Refresh Lokasi</button>
              <input :hidden="hiddenAktif.length === 0 ? false : true" :checked="!shift" @click="setStatusShift()" type="checkbox" class="btn-check" id="btncheck1" autocomplete="off" />
              <label class="btn btn-outline-secondary" for="btncheck1">Shift : {{ statis.shift }} </label>
            </div>
            <div class="text-end" style="min-width: 200px">
              <div class="d-flex align-items-center justify-content-between my-0">
                <div class="form-check form-switch mx-2 ps-1">
                  <input id="tgl-bonus" @click="toggleBonusAll" class="form-check-input mx-1" type="checkbox" :hidden="!shift" />
                </div>
                <button type="button" @click="hadirSemua" :class="['btn', shift ? 'btn-outline-secondary' : 'btn-secondary']">Hadir Semua</button>
              </div>
            </div>
          </div>
        </div>
        <div class="card" style="margin-top: -20px">
          <div class="card-body py-2">
            <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
              <table class="table table-sm table-hover" style="min-width: 1000px">
                <thead>
                  <tr>
                    <th :class="class_th()" :style="style_th()">NO.</th>
                    <th :class="class_th()" :style="style_th()">FOTO</th>
                    <th :class="class_th()" :style="style_th()">NAMA</th>
                    <th :class="class_th()" :style="style_th()">JENIS KELAMIN</th>
                    <th :class="class_th()" :style="style_th()">JABATAN</th>
                    <th :class="class_th()" :style="style_th()">AKSI</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                    <td :class="class_td()" :style="style_td(index + 1)">
                      {{ index + 1 }}
                    </td>
                    <td :class="class_td()" :style="style_td(index + 1)"><img style="height: 30px; width: 30px" :src="getUrl(item.doc.foto)" alt="foto" /></td>
                    <td class="align-middle" :style="style_td(index + 1)" style="min-width: 300px">
                      {{ item.nama }}
                    </td>
                    <td :class="class_td()" :style="style_td(index + 1)">
                      {{ item.jk }}
                    </td>
                    <td :class="class_td()" :style="style_td(index + 1)">{{ item.jabatan.nama }}</td>
                    <td :class="class_td()" :style="style_td(index + 1)" class="d-flex justify-content-end align-items-center">
                      <div class="form-check form-switch" :hidden="shift ? false : true">
                        <input class="form-check-input" type="checkbox" :id="item.uuid" v-model="item.on" @change="toggleBonus(item.uuid)" />
                      </div>
                      <div class="btn-group mx-2" role="group" aria-label="Basic mixed styles example">
                        <button :key="item.uuid" @click="checkInTidakHadir(item.uuid, statis.shift, item.kemampuan.nama, item.jabatan.nama, item.gaji)" type="button" class="btn btn-sm btn-danger" style="min-width: 70px">Alpha</button>
                        <button :key="item.uuid" @click="checkInHadir(item.uuid, item.on ? true : false, statis.shift, item.kemampuan.nama, item.jabatan.nama, item.gaji)" type="button" class="btn btn-sm btn-warning" style="min-width: 70px">
                          Hadir
                        </button>
                        <button :key="item.uuid" @click="openModal(item.uuid)" data-bs-toggle="modal" data-bs-target="#modal-izin" type="button" class="btn btn-sm btn-success" style="min-width: 70px">Izin</button>
                      </div>
                      <div class="modal" id="modal-izin" tabindex="-1">
                        <div class="modal-dialog">
                          <div class="modal-content">
                            <div class="modal-header">
                              <h5 class="modal-title">Keterangan</h5>
                              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div class="modal-body">
                              <form class="row g-3">
                                <div class="col-md-12 text-start">
                                  <label for="input1" class="form-label">Alasan Izin</label>
                                  <input v-model="keterangan" type="text" class="form-control" id="input1" required />
                                </div>
                              </form>
                            </div>
                            <div class="modal-footer">
                              <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                              <button @click="checkInIzin(uuid, statis.shift, item.kemampuan.nama, item.jabatan.nama, item.gaji)" type="button" class="btn btn-primary">Simpan</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenSpinner from '@/components/KomponenSpinner.vue';
import KomponenNotFound from '@/components/KomponenNotFound.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenPagination from '@/components/KomponenPagination.vue';
import KomponenGoogleLokasi from '@/components/KomponenGoogleLokasi.vue';
import router from '@/router';
import { DateTime } from 'luxon';
import { API_KEY } from '../../env';

export default {
  name: 'pageCheckIn',
  components: {
    KomponenJudul,
    KomponenSpinner,
    KomponenNotFound,
    KomponenPagination,
    KomponenGoogleLokasi,
  },
  data() {
    return {
      namaPage: 'Check In',
      loading: false,
      statis: {},
      dataProfilById: {},
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      dataKaryawan: [],
      dataPagination: [],
      shift: false,
      bonusAll: false,
      hiddenAktif: [],
      currentTime: '',
      randomString: '',
      formData: {
        lat: 0,
        lng: 0,
      },
      keterangan: '',
      dataMe: {},
      tanggal: '',
      last_end: {},
      uuid: '',
      errLokasi: '',
      lokasi: false,
    };
  },

  props: {
    searchResults: {
      required: false,
    },
  },

  watch: {
    searchResults: 'getKaryawan',
  },

  methods: {
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(value) {
      if (value % 2 == 0) {
        return 'background-color:#d0d2ff';
      } else {
        return 'background-color:#eff1ff';
      }
    },
    class_td() {
      return 'align-middle text-center';
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getStatisAktif() {
      axios
        .get(`${API_KEY}statis_get_aktif`)
        .then((response) => {
          this.statis = response.data.data;
          this.shift = this.statis.shift === 'Normal';
        })
        .then(() => {
          this.getKaryawan();
        })
        .catch(() => {
          router.push({ name: 'Eror', params: { msg: 'error' } });
        });
    },
    getProfilById() {
      axios
        .get(`${API_KEY}profil/1`)
        .then((response) => {
          this.dataProfilById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getKaryawan() {
      this.loading = true;
      axios
        .get(`${API_KEY}karyawan_get_check_in`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    toggleBonus(uuid) {
      const item = this.dataPagination.find((item) => item.uuid === uuid);
      if (item) {
        item.on === !item.on;
      }
    },
    toggleBonusAll() {
      this.bonusAll = !this.bonusAll;
    },
    setStatusShift() {
      this.loading = true;
      const sts = this.shift;
      axios
        .patch(`${API_KEY}statis_shift_toggle`, {
          id: 1,
          aktif: sts,
        })
        .then((response) => {
          if (response.status === 200) {
            this.getStatisAktif();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
    updateTime() {
      const options = {
        timeZone: 'Asia/Jakarta',
        hour12: false,
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
      };
      this.currentTime = new Date().toLocaleTimeString('id-ID', options);
    },
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getTimeWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone);
      return currentTime.toFormat('HH:mm:ss');
    },
    hadirSemua() {
      if (this.dataKaryawan.length == 0) {
        SweetAlertHelper.error('Gagal!', 'Tidak ada data yang ditambahkan!');
      } else {
        SweetAlertHelper.confirm('Yakin mulai semua?', 'Silahkan klik YES jika yakin!').then((result) => {
          if (result.isConfirmed) {
            this.loading = true;
            axios
              .post(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_hadir_semua' : 'absensi_hadir_semua_no_lokasi'}`, {
                uuid_user: this.dataMe.uuid,
                penjab: this.dataMe.nama,
                tanggal: this.getDateWIB(),
                hadir: 'Hadir',
                keterangan: 'Hadir',
                bonus: this.bonusAll,
                time_start: !this.bonusAll ? this.getTimeWIB() : this.statis.jam_start,
                time_end: null,
                lat: this.formData.lat,
                lng: this.formData.lng,
                shift: this.statis.shift,
                nominal_bonus: this.dataProfilById.nominal_bonus,
              })
              .then((response) => {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
                this.getKaryawan();
              })
              .catch((error) => {
                if (error.response.status === 403) {
                  SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
                }
              })
              .finally(() => {
                this.loading = false;
              });
          }
        });
      }
    },
    checkInHadir(uuid_karyawan, bonus, shift, kemampuan, jabatan, gaji) {
      let data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: uuid_karyawan,
        tanggal: this.getDateWIB(),
        hadir: 'Hadir',
        keterangan: 'Hadir',
        time_start: !bonus ? this.getTimeWIB() : this.statis.jam_start,
        time_end: null,
        shift: shift,
        lat: this.formData.lat,
        lng: this.formData.lng,
        bonus: bonus,
        nominal_bonus: this.dataProfilById.nominal_bonus,
        nominal_gaji: gaji,
        kemampuan: kemampuan,
        jabatan: jabatan,
        penjab: this.dataMe.nama,
      };
      this.save(data);
    },
    checkInTidakHadir(uuid_karyawan, shift, kemampuan, jabatan, gaji) {
      let data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: uuid_karyawan,
        tanggal: this.getDateWIB(),
        hadir: 'Tidak Hadir',
        keterangan: 'Alpha',
        time_start: this.getTimeWIB(),
        time_end: null,
        shift: shift,
        lat: this.formData.lat,
        lng: this.formData.lng,
        bonus: 0,
        nominal_bonus: this.dataProfilById.nominal_bonus,
        nominal_gaji: gaji,
        kemampuan: kemampuan,
        jabatan: jabatan,
        penjab: this.dataMe.nama,
      };
      this.save(data);
    },
    checkInIzin(uuid_karyawan, shift, kemampuan, jabatan, gaji) {
      let data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: uuid_karyawan,
        tanggal: this.getDateWIB(),
        hadir: 'Izin',
        time_start: this.getTimeWIB(),
        time_end: null,
        lat: this.formData.lat,
        lng: this.formData.lng,
        bonus: 0,
        nominal_bonus: this.dataProfilById.nominal_bonus,
        shift: shift,
        nominal_gaji: gaji,
        kemampuan: kemampuan,
        jabatan: jabatan,
        penjab: this.dataMe.nama,
        keterangan: this.keterangan,
      };

      this.save(data);
    },
    openModal(uuid) {
      this.uuid = uuid;
    },
    save(data) {
      SweetAlertHelper.confirm('Yakin mulai?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          this.loading = true;
          axios
            .post(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_presensi_check_in' : 'absensi_presensi_check_in_no_lokasi'}`, data)
            .then((response) => {
              if (response.status === 201) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
                this.getKaryawan();
                window.location.reload();
              }
            })
            .catch((error) => {
              if (error.response.status === 403) {
                SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
              }
            })
            .finally(() => {
              this.loading = false;
            });
        }
      });
    },
    getMe() {
      const token = localStorage.getItem('accessToken');
      this.loading = true;
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .then(() => {
          this.getStatisAktif();
          this.getProfilById();
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    updateFormLokasi(value) {
      this.formData = value;
    },
    refreshLokasi() {
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 8; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
      }
      this.randomString = result;
    },
    dataProfil(value) {
      this.dataProfilById = value;
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    setInterval(this.updateTime, 1000);
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getMe();
      this.getTimeWIB();
      this.getDateWIB();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
