<template>
  <div class="modal fade" id="modal-edit-produksi-user" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Edit Produksi</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4 text-start">
            <div class="col-md-12">
              <label for="e-tmplahir" class="form-label">Jenis Produk</label>
              <select v-model="formData.id_produk" class="form-select" id="e-tmplahir">
                <option selected>---Pilih Jenis Produk---</option>
                <option v-for="item in dataProduk" :key="item.id" :value="item.id">{{ item.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="e-jumlah" class="form-label">Jumlah</label>
              <input v-model="formData.jumlah" type="text" class="form-control" id="e-jumlah" />
            </div>
            <div class="col-md-12">
              <label for="e-jumlah" class="form-label">Tanggal</label>
              <input v-model="formData.tanggal" type="date" class="form-control" id="e-jumlah" />
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="simpanData(formData.id)" class="btn btn-primary">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';

export default {
  name: 'KomponanEditProduksiUser',
  data() {
    return {
      dataProduk: [],
      errResponse: {
        err_required: '',
      },
      formData: {
        id: 0,
        id_produk: '',
        jumlah: 0,
        tanggal: '',
      },
    };
  },

  props: {
    dataProduksi: {
      type: Object,
      required: false,
    },
  },

  watch: {
    dataProduksi: {
      immediate: true,
      handler(newData) {
        this.formData.id = newData.id;
        this.formData.id_produk = newData.id_produk;
        this.formData.jumlah = newData.jumlah;
        let format_yymmdd = newData.tanggal ? newData.tanggal.split('T')[0] : '';
        this.formData.tanggal = format_yymmdd;
      },
    },
  },

  methods: {
    refresProduksi() {
      this.$emit('get-data-produksi');
    },
    simpanData(id) {
      if (!this.formData.id_produk || !this.formData.jumlah || !this.formData.tanggal) {
        this.errResponse.err_required = 'Pastikan data diisi semua!';
      } else {
        this.errResponse.err_required = '';
        axios
          .patch(`${API_KEY}produksi_user_edit/${id}`, this.formData)
          .then((response) => {
            if (response.status === 200) {
              SweetAlertHelper.success('Berhasil!', response.data.msg);
              this.clearForm();
              this.refresProduksi();
            }
          })
          .catch((error) => {
            if (error.response.status == 422) {
              SweetAlertHelper.error('Error!', error);
            } else {
              router.push({ name: 'Eror', params: { msg: error } });
            }
          });
      }
    },
    clearForm() {
      this.formData = {
        id_produk: '',
        jumlah: 0,
        tanggal: '',
      };
      this.errResponse.err_required = '';
    },
    getDataProduk() {
      axios
        .get(`${API_KEY}produk`)
        .then((response) => {
          this.dataProduk = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataProduk();
    } else {
      router.push('/login');
    }
  },
};
</script>
