<template>
  <div class="modal fade" id="modal-edit-karyawan" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Edit Karyawan</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4 text-start">
            <div class="col-md-12">
              <label for="e-nama" class="form-label">Nama</label>
              <input v-model="formData.nama" type="text" class="form-control" id="e-nama" required />
            </div>
            <div class="col-md-12">
              <label for="e-nik" class="form-label">NIK</label>
              <input v-model="formData.no_nik" type="text" class="form-control" id="e-nik" @input="validateNoNIK" />
              <div v-if="errResponse.err_no_nik" class="text-danger fst-italic text-start">{{ errResponse.err_no_nik }}</div>
            </div>
            <div class="col-md-12">
              <label for="lk" class="form-label">Jenis Kelamin</label>
              <div class="d-flex">
                <div class="form-check">
                  <input class="form-check-input" type="radio" v-model="formData.jk" id="lk" value="Laki-laki" checked />
                  <label class="form-check-label" for="lk"> Laki-laki </label>
                </div>
                <div class="form-check ms-4">
                  <input class="form-check-input" type="radio" v-model="formData.jk" id="pr" value="Perempuan" />
                  <label class="form-check-label" for="pr"> Perempuan </label>
                </div>
              </div>
            </div>
            <div class="col-md-12">
              <label for="e-jabatan" class="form-label">Jabatan</label>
              <select v-model="formData.id_jabatan" class="form-select" id="e-jabatan">
                <option selected>---Pilih Jabatan---</option>
                <option v-for="jbt in jabatan" :key="jbt.id" :value="jbt.id">{{ jbt.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="e-tmplahir" class="form-label">Tempat lahir</label>
              <select v-model="formData.tmp_lhr" class="form-select" id="e-tmplahir">
                <option selected>---Pilih Tempat Lahir---</option>
                <option v-for="kab in kota" :key="kab.id" :value="kab.id">{{ kab.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="tgl-lhr" class="pb-2">Tanggal Lahir</label>
              <input v-model="formData.tgl_lhr" type="date" class="form-control" id="tgl-lhr" />
            </div>
            <div class="col-md-12">
              <label for="e-domisili" class="form-label">Domisili</label>
              <select v-model="formData.id_kab" class="form-select" id="e-domisili">
                <option selected>---Pilih Domisili---</option>
                <option v-for="kab in kota" :key="kab.id" :value="kab.id">{{ kab.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="e-notelp" class="form-label">Nomor Telepon</label>
              <input v-model="formData.no_telepon" type="text" class="form-control" id="e-notelp" @input="validateNoTelepon" />
              <span v-if="errResponse.err_no_telepon" class="error-message text-danger fst-italic">{{ errResponse.err_no_telepon }}</span>
            </div>
            <div class="col-md-12">
              <label for="e-norek" class="form-label">Nomor Rekening</label>
              <input v-model="formData.no_rekening" type="text" class="form-control" id="e-norek" @input="validateNoRekening" />
              <span v-if="errResponse.err_rekening" class="error-message text-danger fst-italic">{{ errResponse.err_rekening }}</span>
            </div>
            <div class="col-md-12">
              <label for="e-skil" class="form-label">Tingkat Skill</label>
              <select v-model="formData.id_kemampuan" class="form-select" id="e-skil">
                <option selected>---Pilih Skill---</option>
                <option v-for="skl in skill" :key="skl.id" :value="skl.id">{{ skl.nama }}</option>
              </select>
            </div>
          </form>
          <!-- End Multi Columns Form -->
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="simpanData(formData.uuid)" class="btn btn-primary" data-bs-dismiss="modal">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';

export default {
  name: 'KomponanEditKaryawan',
  props: {
    dataKar: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      formData: {
        uuid: '',
        nama: '',
        no_nik: '',
        jk: '',
        id_jabatan: '',
        tmp_lhr: '',
        tgl_lhr: '',
        id_kab: '',
        no_telepon: '',
        no_rekening: '',
        id_kemampuan: '',
      },
      errResponse: {
        err_required: '',
        err_no_telepon: '',
        err_no_nik: '',
        err_rekening: '',
      },
      kota: [],
      jabatan: [],
      skill: [],
    };
  },

  watch: {
    dataKar: {
      immediate: true,
      handler(newData) {
        this.formData.uuid = newData.uuid;
        this.formData.nama = newData.nama;
        this.formData.no_nik = newData.no_nik;
        this.formData.jk = newData.jk;
        this.formData.id_jabatan = newData.id_jabatan;
        this.formData.tmp_lhr = newData.tmp_lhr;
        this.formData.tgl_lhr = newData.tgl_lhr;
        this.formData.id_kab = newData.id_kab;
        this.formData.no_telepon = newData.no_telepon;
        this.formData.no_rekening = newData.no_rekening;
        this.formData.id_kemampuan = newData.id_kemampuan;
      },
    },
  },

  methods: {
    validateNoTelepon() {
      const teleponPattern = /^\d{11,13}$/;
      if (!teleponPattern.test(this.formData.no_telepon)) {
        this.errResponse.err_no_telepon = 'Nomor telepon harus terdiri dari 11 hingga 13 digit angka.';
      } else {
        this.errResponse.err_no_telepon = '';
      }
    },

    validateNoNIK() {
      const teleponPattern = /^\d{16,16}$/;
      if (!teleponPattern.test(this.formData.no_nik)) {
        this.errResponse.err_no_nik = 'Nomor NIK harus terdiri dari 16 digit angka.';
      } else {
        this.errResponse.err_no_nik = '';
      }
    },
    validateNoRekening() {
      const teleponPattern = /^\d{09,16}$/;
      if (!teleponPattern.test(this.formData.no_rekening)) {
        this.errResponse.err_rekening = 'Nomor rekening harus terdiri dari 9 sampai 16 digit angka.';
      } else {
        this.errResponse.err_rekening = '';
      }
    },

    async getKota() {
      const response = await axios.get(`${API_KEY}kabupaten`);
      this.kota = response.data.data;
    },
    async getSkill() {
      const response = await axios.get(`${API_KEY}kemampuan`);
      this.skill = response.data.data;
    },
    async getJabatan() {
      const response = await axios.get(`${API_KEY}jabatan`);
      this.jabatan = response.data.data;
    },

    refresKaryawan() {
      this.$emit('get-data-karyawan');
    },

    simpanData(uuid) {
      if (
        !this.formData.nama ||
        !this.formData.no_nik ||
        !this.formData.jk ||
        !this.formData.id_jabatan ||
        !this.formData.tmp_lhr ||
        !this.formData.tgl_lhr ||
        !this.formData.id_kab ||
        !this.formData.no_telepon ||
        !this.formData.no_rekening ||
        !this.formData.id_kemampuan
      ) {
        this.errResponse.err_required = 'Pastikan data diisi semua!';
      } else {
        this.errResponse = {
          err_required: '',
        };
        try {
          axios
            .patch(`${API_KEY}karyawan_update_by_uuid/${uuid}`, this.formData)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
                router.push({ name: 'pageKaryawanAktif', params: { msg: response.data.msg } });
                this.refresKaryawan();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error.message } });
            });
        } catch (error) {
          router.push({ name: 'Eror', params: { msg: error } });
        }
      }
    },
    clearForm() {
      (this.formData = {
        nama: '',
        email: '',
        password: '',
        confirm_password: '',
        no_nik: '',
        jk: 'Laki-laki',
        id_jabatan: '',
        tmp_lhr: '',
        tgl_lhr: '',
        id_kab: '',
        no_telepon: '',
        no_rekening: '',
        id_kemampuan: '',
      }),
        (this.errResponse = {
          err_required: '',
          err_email: '',
          err_password: '',
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getKota();
      this.getJabatan();
      this.getSkill();
    } else {
      router.push('/login');
    }
  },
};
</script>
