<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard">
      <div class="card">
        <div class="card-body">
          <form class="row g-3 ps-1 pe-1">
            <div class="col-md-12">
              <div class="form-floating mb-3">
                <input disabled v-model="this.dataMe.no_nik" type="email" class="form-control" id="floatingNik" placeholder="name@example.com" />
                <label for="floatingNik">NIK</label>
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-floating mb-3">
                <input disabled v-model="this.dataMe.nama" type="text" class="form-control" id="floatingNama" placeholder="Password" />
                <label for="floatingNama">Nama</label>
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-floating mb-3">
                <input disabled v-model="tanggal" type="date" class="form-control" id="floatingTanggal" placeholder="name@example.com" required />
                <label for="floatingTanggal">Tanggal</label>
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-floating">
                <textarea v-model="this.keterangan" class="form-control" placeholder="Leave a comment here" id="floatingTextarea" required></textarea>
                <label for="floatingTextarea">Keterangan</label>
              </div>
            </div>
            <div class="text-danger fst-italic text-center">{{ err_required }}</div>
            <div class="col-md-12">
              <div class="form-check text-end">
                <button @click="izin" type="button" class="btn btn-primary">Tambah</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenJudul from '../components/KomponenJudul.vue';
import { DateTime } from 'luxon';
import { API_KEY } from '../../env';
import axios from 'axios';
import router from '@/router';

export default {
  name: 'pagePengajuanIzin',
  components: {
    KomponenJudul,
  },
  data() {
    return {
      namaPage: 'Pengajuan Izin',
      tanggal: '',
      keterangan: '',
      err_required: '',
      dataMe: {},
      statis: {},
    };
  },

  methods: {
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      this.tanggal = currentDateWIB.toFormat('yyyy-MM-dd');
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getTimeWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone);
      return currentTime.toFormat('HH:mm:ss');
    },

    getStatisAktif() {
      axios
        .get(`${API_KEY}statis_get_aktif`)
        .then((response) => {
          this.statis = response.data.data;
          this.shift = this.statis.shift === 'Normal';
        })
        .catch(() => {
          router.push({ name: 'Eror', params: { msg: 'error' } });
        });
    },

    getMe() {
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    izin() {
      if (!this.tanggal || !this.keterangan) {
        this.err_required = 'Pastikan data diisisemua!';
      } else {
        this.tambah();
      }
    },

    tambah() {
      const data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: this.dataMe.uuid,
        tanggal: this.getDateWIB(),
        hadir: 'Izin',
        time_start: this.getTimeWIB(),
        time_end: null,
        bonus: 0,
        nominal_bonus: this.statis.nominal_bonus,
        shift: this.statis.shift,
        nominal_gaji: this.dataMe.nominal_gaji,
        kemampuan: this.dataMe.kemampuan.nama,
        jabatan: this.dataMe.jabatan.nama,
        penjab: this.dataMe.nama,
        keterangan: this.keterangan,
      };

      SweetAlertHelper.confirm('Yakin mengajukan izin?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          axios
            .post(`${API_KEY}absensi_pengajuan_izin`, data)
            .then((response) => {
              if (response.status === 201) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getMe();
      this.getDateWIB();
      this.getTimeWIB();
      this.getStatisAktif();
    } else {
      router.push('/login');
    }
  },
};
</script>
