import { createRouter, createWebHistory } from 'vue-router';
import LoginView from '../views/LoginView.vue';
import MainView from '../views/MainView .vue';

import pageDashboard from '@/pages/pageDashboard.vue';
import pageKaryawanAktif from '../pages/pageKaryawanAktif.vue';
import pageKaryawanNonAktif from '../pages/pageKaryawanNonAktif.vue';
import pageUsers from '@/pages/pageUsers.vue';
import PageKaryawanLokasi from '@/pages/pageKaryawanLokasi.vue';
import pageRiwayatAbsensi from '@/pages/pageRiwayatAbsensi.vue';
import pageLapAbsensi from '@/pages/pageLapAbsensi.vue';
import pageLapGaji from '@/pages/pageLapGaji.vue';
import pageProfil from '@/pages/pageProfil.vue';
import pageCheckIn from '@/pages/pageCheckIn.vue';
import pageCheckOut from '@/pages/pageCheckOut.vue';
import pagePresensi from '@/pages/pagePresensi.vue';
import pageKaryawanHakAkses from '@/pages/pageKaryawanHakAkses.vue';
import pagePengajuanIzin from '@/pages/pagePengajuanIzin.vue';
import ErorView from '../components/ErorView.vue';
import KomponenTambahLokasi from '@/components/KomponenTambahLokasi.vue';
import KomponenEditLokasi from '@/components/KomponenEditLokasi.vue';
import permissionMiddleware from '@/middleware/permissionMiddleware';
import pageInformasiPenggajian from '@/pages/pageInformasiPenggajian.vue';
import pagePemulihan from '@/pages/pagePemulihan.vue';
import pagePengaturanView from '@/pages/pagePengaturanView.vue';
import pageDataAbsensiVue from '@/pages/pageDataAbsensi.vue';
import pageDataProduksiVue from '@/pages/pageDataProduksi.vue';
import pageDataProduksiUser from '@/pages/pageDataProduksiUser.vue';

const routes = [
  {
    path: '/login',
    name: 'LoginView',
    component: LoginView,
  },
  {
    path: '/eror/:msg',
    name: 'Eror',
    component: ErorView,
  },
  {
    path: '/dashboard',
    name: 'Main',
    component: MainView,
    children: [
      {
        path: '/',
        name: 'pageDashboard',
        component: pageDashboard,
      },
      {
        path: '/presensi',
        name: 'pagePresensi',
        component: pagePresensi,
      },
      {
        path: '/data-produksi-me',
        name: 'pageDataProduksiUser',
        component: pageDataProduksiUser,
      },
      {
        path: '/check_in',
        name: 'pageCheckIn',
        component: pageCheckIn,
      },
      {
        path: '/check_out',
        name: 'pageCheckOut',
        component: pageCheckOut,
      },
      {
        path: '/pemulihan',
        name: 'pagePemulihan',
        component: pagePemulihan,
      },
      {
        path: '/riwayat_absensi',
        name: 'pageRiwayatAbsensi',
        component: pageRiwayatAbsensi,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-absensi',
        name: 'pageDataAbsensi',
        component: pageDataAbsensiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-produksi',
        name: 'pageDataProduksi',
        component: pageDataProduksiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },

      // START MASTER DATA
      {
        path: '/k/:randomString',
        name: 'pageKaryawanAktif',
        component: pageKaryawanAktif,
      },
      {
        path: '/kn/:randomString',
        name: 'pageKaryawanNonAktif',
        component: pageKaryawanNonAktif,
      },
      {
        path: '/p/:randomString',
        name: 'pageInformasiPenggajian',
        component: pageInformasiPenggajian,
        // beforeEnter: (to, from, next) => {
        //   const id_path = localStorage.getItem('id-path');
        //   const fullUrl = window.location.href;
        //   if (id_path === fullUrl) {
        //     next();
        //   } else {
        //     next('/halaman_error');
        //   }
        // },
      },
      {
        path: '/l/:randomString',
        name: 'pageKaryawanLokasi',
        component: PageKaryawanLokasi,
      },
      {
        path: '/u/:randomString',
        name: 'pageUsers',
        component: pageUsers,
      },
      {
        path: '/h/:randomString',
        name: 'pageKaryawanHakAkses',
        component: pageKaryawanHakAkses,
      },
      // END MASTER DATA
      {
        path: '/profile/:randomString',
        name: 'pageProfil',
        component: pageProfil,
      },
      {
        path: `/lokasi-tambah`,
        name: 'KomponenTambahLokasi',
        component: KomponenTambahLokasi,
      },
      {
        path: `/lokasi-edit`,
        name: 'KomponenEditLokasi',
        component: KomponenEditLokasi,
      },
      {
        path: '/pengajuan_izin',
        name: 'pagePengajuanIzin',
        component: pagePengajuanIzin,
      },
      {
        path: '/presensi',
        name: 'pagePresensi',
        component: pagePresensi,
      },
      {
        path: '/lap_absensi',
        name: 'pageLapAbsensi',
        component: pageLapAbsensi,
      },
      {
        path: '/lap_gaji',
        name: 'pageLapGaji',
        component: pageLapGaji,
      },
      {
        path: '/pengaturan',
        name: 'pagePengaturan',
        component: pagePengaturanView,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  mode: 'history',
});

export default router;
