<template>
  <main id="main-padding" class="main">
    <komponen-judul-vue :title="namaPage" />

    <section class="section dashboard">
      <div class="row">
        <div class="col-lg-4">
          <div class="row px-1">
            <div class="card">
              <div class="card-body p-3">
                <form class="row g-3">
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Nama</label>
                    <input disabled type="text" class="form-control" id="input1" required :value="dataKaryawan ? dataKaryawan.nama : ''" />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Jabatan</label>
                    <input disabled type="text" class="form-control" id="input1" required :value="dataKaryawan ? dataKaryawan.jabatan : ''" />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Latitude</label>
                    <input disabled type="text" class="form-control" id="input1" required v-model="formData.lat" />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Longitude</label>
                    <input disabled type="text" class="form-control" id="input1" required v-model="formData.lng" />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Lokasi</label>
                    <input v-model="nama_lokasi" type="text" class="form-control" id="input1" required />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Alamat</label>
                    <input v-model="alamat" type="text" class="form-control" id="input1" required />
                  </div>
                  <div class="col-md-12">
                    <label for="input1" class="form-label">Max Radius</label>
                    <input v-model="max_jarak" type="number" class="form-control" id="input1" required />
                  </div>
                  <div class="col-md-12">
                    <div class="form-check">
                      <input v-model="aktif" class="form-check-input" type="checkbox" value="1" id="flexCheckDefault" />
                      <label class="form-check-label" for="flexCheckDefault"> Default checkbox </label>
                    </div>
                  </div>
                  <div class="text-danger fst-italic text-center">{{ err_required }}</div>
                  <div class="col-md-12 d-flex justify-content-between">
                    <button @click="refreshLokasi" type="button" class="btn btn-success">Ambil Lokasi</button>
                    <button @click="simpan(dataKaryawan ? dataKaryawan.uuid : '')" type="button" class="btn btn-primary">Simpan</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="row px-1">
            <div class="card">
              <div class="card-body p-3">
                <komponen-google-lokasi :reload-lokasi="randomString" @update-form-lokasi="updateFormLokasi" @data-profil="dataProfil" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import KomponenJudulVue from './KomponenJudul.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenGoogleLokasi from '@/components/KomponenGoogleLokasi.vue';
import axios from 'axios';
import router from '@/router';
import { API_KEY } from '../../env';

export default {
  name: 'TambahLokasi',
  components: {
    KomponenJudulVue,
    KomponenGoogleLokasi,
  },

  data() {
    return {
      dataKaryawan: null,
      namaPage: 'Set Lokasi',

      err_required: '',
      randomString: '',
      dataProfilById: {},
      formData: {
        lat: 0,
        lng: 0,
      },
      nama_lokasi: '',
      alamat: '',
      max_jarak: '',
      aktif: '',
    };
  },

  methods: {
    simpan(uuid_karyawan) {
      let data = {
        uuid_karyawan: uuid_karyawan,
        nama: this.nama_lokasi,
        lat: this.formData.lat,
        lng: this.formData.lng,
        max_jarak: this.max_jarak,
        alamat: this.alamat,
        aktif: this.aktif,
      };
      if (!this.nama_lokasi || !this.formData.lat || !this.formData.lng || !this.alamat || !this.max_jarak || !this.aktif) {
        this.err_required = 'Pastikan data diisi semua!';
      } else {
        axios
          .post(`${API_KEY}lokasi_add/`, data)
          .then((response) => {
            if (response.status == 201) {
              SweetAlertHelper.success('Berhasil!', response.data.msg);
              router.push({
                name: 'pageKaryawanLokasi',
                params: { randomString: 'iq6B4pOSkcePiESTv2y3AuIwupCqNsV0jLyhAfDx226caRL9RlKfoFjbYaajuXNalSEJsp01CQKnYcIV0L6PzKnnbDQZuY4bodpp', msg: response.data.msg },
              });
            }
          })
          .catch((err) => {
            router.push({ name: 'Eror', params: { msg: err } });
          });
      }
    },
    getDataKaryawanById() {
      const storedData = localStorage.getItem('uuid');
      if (storedData) {
        const data = JSON.parse(storedData);
        this.dataKaryawan = data;
      }
    },
    updateFormLokasi(value) {
      this.formData = value;
    },
    refreshLokasi() {
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 8; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
      }
      this.randomString = result;
    },
    dataProfil(value) {
      this.dataProfilById = value;
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataKaryawanById();
    } else {
      router.push('/login');
    }
  },
};
</script>
