<template>
  <div>
    <komponen-spinner v-if="loading" />
    <div class="fst-italic d-flex justify-content-between overflow-auto">
      <p style="font-size: 12px" class="text-danger">Pastikan internet stabil untuk mengambil coordinate yang akurat.</p>
      <p style="font-size: 12px">
        <span>Latitude : {{ formData.lat }} </span>, <span>Longitude : {{ formData.lng }}</span>
      </p>
    </div>
    <div style="margin-top: -10px" v-if="online">
      <GoogleMap :api-key="api_key" style="width: 100%; height: 175px" :center="formData" :zoom="16">
        <Marker :options="{ position: formData, draggable: true, title: 'Location' }" @dragend="onformDataChanged" />
      </GoogleMap>
    </div>
    <div class="mt-4 pt-2 px-4" v-else>
      <h6 class="text-center py-4 text-danger">{{ errLokasi }}</h6>
    </div>
  </div>
</template>

<script>
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { GoogleMap, Marker } from 'vue3-google-map';
import { API_KEY } from '../../env';
import axios from 'axios';
import router from '@/router';
import KomponenSpinner from './KomponenSpinner.vue';

export default {
  name: 'KomponenGoogleLokasi',
  components: {
    GoogleMap,
    Marker,
    KomponenSpinner,
  },

  data() {
    return {
      loading: false,
      errLokasi: '',
      api_key: 'AIzaSyA91TxyxpAZRkXQNLMzkP1_XZVTvkdmQ_I',
      formData: {
        lat: 0,
        lng: 0,
      },
      useLokasi: false,
      dataProfilById: {},
      online: false,
    };
  },

  mounted() {
    this.getProfilById();
    this.onlineStatus();
    if (this.dataForm) {
      this.formData = this.dataForm;
    } else {
      this.autoLocation();
    }
    // Mengatur penanganan kesalahan secara global
    window.onerror = function (message, source, lineno, colno, error) {
      console.error('Terjadi kesalahan:', message);
      console.error('Terjadi kesalahan:', source);
      console.error('Terjadi kesalahan:', lineno);
      console.error('Terjadi kesalahan:', colno);
      console.error('Terjadi kesalahan:', error);
      // Tambahkan logika lain yang diperlukan di sini
    };
  },

  props: {
    reloadLokasi: {
      type: String,
      required: true,
    },
    dataForm: {
      required: false,
    },
  },
  watch: {
    reloadLokasi: {
      immediate: true,
      handler(value) {
        if (value) {
          this.getLocation();
        }
      },
    },
  },

  methods: {
    onlineStatus() {
      this.online = navigator.onLine;
    },

    getProfilById() {
      this.loading = true;
      axios
        .get(`${API_KEY}profil/1`)
        .then((response) => {
          this.dataProfilById = response.data.data;
        })
        .then(() => {
          this.$emit('data-profil', this.dataProfilById);
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    autoLocation() {
      try {
        this.loading = true;
        if (navigator.onLine) {
          this.isLoadingLocation = true;
          navigator.geolocation.getCurrentPosition(this.onSuccess.bind(this), this.onGeolocationError.bind(this), { enableHighAccuracy: true, timeout: 5000, maximumAge: 0 });
        } else {
          // SweetAlertHelper.error('Error', 'No internet connection. Using default location.');
          this.errLokasi = 'Error, No internet connection. Using default location.';
        }
      } catch (err) {
        SweetAlertHelper.error('Error', `Gagal memuat Google Maps API: ${err.message}`);
        this.errLokasi = `Error, Gagal memuat Google Maps API: ${err.message}`;
      } finally {
        this.loading = false;
      }
    },
    getLocation() {
      try {
        this.loading = true;
        if (navigator.onLine) {
          this.isLoadingLocation = true;
          navigator.geolocation.getCurrentPosition(this.onSuccess.bind(this), this.onGeolocationError.bind(this), { enableHighAccuracy: true, timeout: 5000, maximumAge: 0 });
        } else {
          SweetAlertHelper.error('Error', 'No internet connection. Using default location.');
          this.errLokasi = 'Error, No internet connection. Using default location.';
        }
      } catch (err) {
        SweetAlertHelper.error('Error', `Gagal memuat Google Maps API: ${err.message}`);
        this.errLokasi = `Error, Gagal memuat Google Maps API: ${err.message}`;
      } finally {
        this.loading = false;
      }
    },
    onGeolocationError(error) {
      switch (error.code) {
        case error.PERMISSION_DENIED:
          SweetAlertHelper.error('Error', 'User denied the request for Geolocation.');
          this.errLokasi = 'Error, User denied the request for Geolocation.';
          break;
        case error.POSITION_UNAVAILABLE:
          SweetAlertHelper.error('Error', 'Location information is unavailable.');
          this.errLokasi = 'Error, Location information is unavailable.';
          break;
        case error.TIMEOUT:
          SweetAlertHelper.error('Error', 'The request to get user location timed out.');
          this.errLokasi = 'Error, The request to get user location timed out.';
          break;
        case error.UNKNOWN_ERROR:
          SweetAlertHelper.error('Error', 'An unknown error occurred while trying to get user location.');
          this.errLokasi = 'Error, An unknown error occurred while trying to get user location.';
          break;
        default:
          SweetAlertHelper.error('Error', 'An error occurred while trying to get user location.');
          this.errLokasi = 'Error, An error occurred while trying to get user location.';
      }
    },
    onSuccess(position) {
      this.formData = {
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      };
      this.$emit('update-form-lokasi', this.formData);
    },
    onformDataChanged(newPosition) {
      const latLngObject = newPosition.latLng;
      const lat = latLngObject.lat();
      const lng = latLngObject.lng();
      this.formData.lat = lat.toFixed(7);
      this.formData.lng = lng.toFixed(7);
      this.$emit('update-form-lokasi', this.formData);
    },
  },
};
</script>
