<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card">
        <div class="card-body">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover align-middle bg-danger" style="font-size: 14px; min-width: 2500px">
              <thead>
                <tr>
                  <th scope="col" class="text-center align-middle">No.</th>
                  <th scope="col" class="text-center align-middle">Profile</th>
                  <th scope="col" class="text-center align-middle">Nomor NIK</th>
                  <th scope="col" class="text-center align-middle">Nama</th>
                  <th scope="col" class="text-center align-middle">Jenis Kelamin</th>
                  <th scope="col" class="text-center align-middle">Karyawan</th>
                  <th scope="col" class="text-center align-middle">Lokasi</th>
                  <th scope="col" class="text-center align-middle">Absensi</th>
                  <th scope="col" class="text-center align-middle">Produksi</th>
                  <th scope="col" class="text-center align-middle">Produk</th>
                  <th scope="col" class="text-center align-middle">Shift</th>
                  <th scope="col" class="text-center align-middle">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td class="align-middle text-center">
                    {{ index + 1 }}
                  </td>
                  <td class="align-middle text-center">
                    <img style="height: 40px; width: 40px" alt="foto" :src="item.doc === null ? 'Tidak ada foto' : getUrl(item.doc.foto)" />
                  </td>
                  <td class="align-middle text-center">
                    {{ Number.parseFloat(item.no_nik).toFixed(0) }}
                  </td>
                  <td class="align-middle">
                    {{ item.nama }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.jk }}
                  </td>
                  <td class="align-middle text-center" v-for="(value, keys) in item.akses" :key="keys">
                    <span v-for="(val, key) in JSON.parse(value)" :key="key" class="d-inline-block">
                      <span class="form-check text-start mx-2">
                        <input class="form-check-input" type="checkbox" value="val" :id="'checkbox_' + item.uuid + keys + '_' + key" :checked="val == true" @change="updateAccess(item.uuid, keys, key, $event.target.checked)" />
                        <label class="form-check-label" :for="'checkbox_' + item.uuid + keys + '_' + key"> {{ key }}</label>
                      </span>
                    </span>
                  </td>
                  <td class="align-middle text-center">
                    <button @click="simpanData(item.uuid)" class="btn btn-success btn-sm">Update</button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<style>
.form-check-input {
  transform: scale(1.5);
}
</style>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';
import router from '@/router';

export default {
  name: 'pageKaryawanHakAkses',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
  },

  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      namaPage: 'Hak Akses',
      dataKaryawan: [],
      dataPagination: [],
      dataObjectAkses: {},
    };
  },

  props: {
    searchResults: {
      required: true,
    },
  },

  watch: {
    searchResults: 'getDataKaryawan',
  },

  methods: {
    simpanData(uuid) {
      try {
        axios
          .patch(`${API_KEY}akses_update_by_uuid/${uuid}`, this.dataObjectAkses)
          .then((response) => {
            if (response.status === 200) {
              SweetAlertHelper.success('Berhasil!', response.data.msg);
            }
          })
          .then(() => {
            this.getDataKaryawan();
          })
          .catch((error) => {
            router.push({ name: 'Eror', params: { msg: error } });
          });
      } catch (error) {
        router.push({ name: 'Eror', params: { msg: error } });
      }
    },
    updateAccess(uuid, keys, key, value) {
      const item = this.dataPagination.find((item) => item.uuid === uuid);
      if (item) {
        if (keys === 'hk_absensi' || keys === 'hk_karyawan' || keys === 'hk_lokasi' || keys === 'hk_produk' || keys === 'hk_produksi' || keys === 'hk_shift') {
          const aksesObj = JSON.parse(item.akses[keys]);
          aksesObj[key] = value;
          item.akses[keys] = JSON.stringify(aksesObj);
          this.dataObjectAkses = item.akses;
        }
      }
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getDataKaryawan() {
      axios
        .get(`${API_KEY}akses_get_all`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    update(uuid) {
      axios
        .patch(`${API_KEY}hak_akses_update_by_uuid/${uuid}`, this.item.hak_akses)
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
  },

  computed: {
    convertFormatText(number) {
      return Number.parseFloat(number).toFixed(0);
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataKaryawan();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
