<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataUser.length > 0">
      <div class="card">
        <div class="card-body">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="font-size: 14px; min-width: 1000px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" class="text-center">No.</th>
                  <th scope="col" class="text-center">Profile</th>
                  <th scope="col" class="text-center">Nama</th>
                  <th scope="col" class="text-center">Email</th>
                  <th scope="col" class="text-center">Password</th>
                  <th scope="col" class="text-center">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td class="align-middle text-center">
                    {{ index + 1 }}
                  </td>
                  <td class="align-middle text-center">
                    <img style="height: 40px; width: 40px" alt="foto" :src="item.doc === null ? 'Tidak ada foto' : getUrl(item.doc.foto)" />
                  </td>
                  <td class="align-middle">
                    {{ item.nama }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.email }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.password }}
                  </td>
                  <td class="align-middle text-center">
                    <button @click="getByUuid(item.uuid)" class="btn btn-success btn-md mx-1" data-bs-toggle="modal" data-bs-target="#modal-edit-user">
                      <i class="bi bi-pencil-square"></i>
                    </button>
                  </td>
                  <komponen-edit-user :data-user="userById" @get-data-user="getDataUser" />
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataUser" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenEditUser from '../components/KomponenEditUser.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import router from '@/router';
import { API_KEY } from '../../env';

export default {
  name: 'pageUsers',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenEditUser,
    KomponenPagination,
  },

  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      namaPage: 'Akun User',
      dataUser: [],
      dataPagination: [],
      currentPage: 1,
      itemsPerPage: 20,
      maxDisplayedPages: 10,
      userById: {},
      hoverIndex: null,
      errResponse: {
        err_password: '',
      },
    };
  },

  props: {
    searchResults: {
      required: false,
    },
  },

  watch: {
    searchResults: 'getDataUser',
  },

  methods: {
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getDataUser() {
      axios
        .get(`${API_KEY}user_get_all_aktif`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataUser = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getByUuid(uuid) {
      axios
        .get(`${API_KEY}user_get_by_uuid/${uuid}`)
        .then((response) => {
          this.userById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
  },

  computed: {
    convertFormatText(number) {
      return Number.parseFloat(number).toFixed(0);
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataUser();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
