<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card">
        <div class="card-body">
          <button type="button" class="btn btn-primary m-2" data-bs-toggle="modal" data-bs-target="#modal-tambah-karyawan">+ Tambah</button>
          <komponen-tambah-karyawan @get-data-karyawan="getDataKaryawan" />
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="font-size: 14px; min-width: 1500px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" class="text-center">No.</th>
                  <th scope="col" class="text-center">Profile</th>
                  <th scope="col" class="text-center">Nomor NIK</th>
                  <th scope="col" class="text-center">Nama</th>
                  <th scope="col" class="text-center">Jenis Kelamin</th>
                  <th scope="col" class="text-center">Jabatan</th>
                  <th scope="col" class="text-center">Tempat & Tanggal Lahir</th>
                  <th scope="col" class="text-center">Kabupaten Domisili</th>
                  <th scope="col" class="text-center">Kemampuan</th>
                  <th scope="col" class="text-center">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td class="align-middle text-center">
                    {{ index + 1 }}
                  </td>
                  <td @mouseover="hoverIndex = index" @mouseout="hoverIndex = null" class="align-middle text-center">
                    <img v-if="!(hoverIndex === index)" style="height: 40px; width: 40px" alt="foto" :src="item.doc && item.doc.foto ? getUrl(item.doc.foto) : getUrl('profilKosong.jpg')" />
                    <a v-if="hoverIndex === index" type="button" @click="toggleShow(item.uuid)" class="icon-upload btn btn-success btn-md" title="Upload new profile image"><i class="bi bi-upload"></i></a>
                  </td>
                  <td class="align-middle text-center">
                    {{ Number.parseFloat(item.no_nik).toFixed(0) }}
                  </td>
                  <td class="align-middle">
                    {{ item.nama }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.jk }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.jabatan.nama }}
                  </td>
                  <td class="align-middle text-center">{{ item.tmpLahir.nama.split(' ')[1] }}, {{ convertLocal(item.tgl_lhr) }}</td>
                  <td class="align-middle text-center">
                    {{ item.alamat.nama }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.kemampuan.nama }}
                  </td>
                  <td class="align-middle text-center">
                    <div class="btn-group" role="group" aria-label="Basic mixed styles example">
                      <button @click="setStatusNonaktif(item.uuid)" type="button" class="btn btn-danger"><i class="bi bi-x-octagon"></i></button>
                      <button @click="getKaryawanByUuid(item.uuid)" data-bs-toggle="modal" data-bs-target="#modal-edit-karyawan" type="button" class="btn btn-warning"><i class="bi bi-pencil-square"></i></button>
                    </div>
                    <komponen-edit-karyawan :data-kar="karyawanById" @get-data-karyawan="getDataKaryawan" />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
      <my-upload
        @crop-success="cropSuccess"
        @crop-upload-success="cropUploadSuccess"
        @crop-upload-fail="cropUploadFail"
        v-model="show"
        :method="method"
        :field="field"
        :width="200"
        :height="200"
        :maxSize="maxSize"
        :url="url"
        :lang-ext="langExt"
        :headers="headers"
        :allow-img-format="allowImgFormat"
      />
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenTambahKaryawan from '../components/KomponenTambahKaryawan.vue';
import KomponenEditKaryawan from '../components/KomponenEditKaryawan.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import SweetAlertHelper from '../SweetAlertHelper/SweetAlertHelper';
import myUpload from 'vue-image-crop-upload';
import router from '@/router';
import { API_KEY } from '../../env';

export default {
  name: 'pageKaryawanAktif',
  components: {
    KomponenJudul,
    KomponenTambahKaryawan,
    KomponenNotFound,
    KomponenEditKaryawan,
    KomponenPagination,
    'my-upload': myUpload,
  },

  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      hoverIndex: null,
      index: 0,
      namaPage: 'Karyawan',
      dataKaryawan: [],
      dataPagination: [],
      karyawanById: {},
      uuid_karyawan: '',
      show: false,
      url: '',
      imgDataUrl: '',
    };
  },

  props: {
    searchResults: {
      required: true,
    },
    field: {
      type: String,
      default: 'file',
    },
    headers: {
      type: Object,
      default: () => null,
    },
    width: {
      type: Number,
      default: 200,
    },
    height: {
      type: Number,
      default: 200,
    },
    noRotate: {
      type: Boolean,
      default: true,
    },
    noCircle: {
      type: Boolean,
      default: false,
    },
    noSquare: {
      type: Boolean,
      default: false,
    },
    maxSize: {
      type: Number,
      default: 2048,
    },
    langType: {
      type: String,
      default: 'id',
    },
    langExt: {
      type: Object,
      default: () => ({
        hint: 'Klik atau seret file ke sini untuk mengunggah',
        loading: 'Mengunggah…',
        noSupported: 'Browser tidak didukung, silakan gunakan IE10+ atau browser lainnya',
        success: 'Unggahan berhasil',
        fail: 'Gagal mengunggah',
        preview: 'Pratinjau',
        btn: {
          off: 'Batal',
          close: 'Tutup',
          back: 'Kembali',
          save: 'Simpan',
        },
        error: {
          onlyImg: 'Gunakan format gambar.',
          outOfSize: 'Gambar melebihi batas ukuran: ',
          lowestPx: 'Ukuran gambar terlalu kecil. Setidaknya diharapkan: ',
        },
      }),
    },
    imgBgc: {
      type: String,
      default: '#000000',
    },
    withCredentials: {
      type: Boolean,
      default: false,
    },
    method: {
      type: String,
      default: 'PATCH',
    },
    initialImgUrl: {
      type: String,
      default: '',
    },
    allowImgFormat: {
      type: Array,
      default: () => ['JPG', 'PNG'],
    },
  },

  watch: {
    searchResults: 'getDataKaryawan',
    uuid_karyawan: {
      handler: 'updateUrl',
      deep: true,
    },
  },
  methods: {
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    convertLocal(dateString) {
      const dateArray = dateString.split('-');
      const dateObject = new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);
      const day = dateObject.getDate();
      const monthIndex = dateObject.getMonth();
      const year = dateObject.getFullYear();
      const monthNames = ['Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];
      const monthName = monthNames[monthIndex];
      const reversedAndFormattedDate = `${day} ${monthName} ${year}`;
      return reversedAndFormattedDate;
    },
    getDataKaryawan() {
      axios
        .get(`${API_KEY}karyawan_get_all_aktif`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getKaryawanByUuid(uuid) {
      axios
        .get(`${API_KEY}karyawan_get_by_uuid/${uuid}`)
        .then((response) => {
          this.karyawanById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    setStatusNonaktif(uuid) {
      SweetAlertHelper.confirm('Apakah yakin?', 'Data akann di non aktifkan.').then((result) => {
        if (result.isConfirmed) {
          axios
            .patch(`${API_KEY}karyawan_set_status/${uuid}`, {
              status: 'Non Aktif',
            })
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', 'Data telah di non aktifkan.');
                this.getDataKaryawan();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },

    toggleShow(uuid) {
      this.show = !this.show;
      this.uuid_karyawan = uuid;
    },
    cropSuccess(imgDataUrl) {
      this.imgDataUrl = imgDataUrl;
    },
    cropUploadSuccess(jsonData) {
      if (jsonData.code === 200) {
        this.show = !this.show;
        SweetAlertHelper.success('Berhasil!', jsonData.msg);
        this.getDataKaryawan();
      }
    },
    cropUploadFail(status) {
      this.show = !this.show;
      SweetAlertHelper.error('Gagal!', status);
      this.getDataKaryawan();
    },
    updateUrl() {
      this.url = `${API_KEY}doc_foto_by_uuid/${this.uuid_karyawan}`;
    },
  },
  created() {
    this.updateUrl();
  },

  computed: {
    convertFormatText(number) {
      return Number.parseFloat(number).toFixed(0);
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataKaryawan();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
