<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataProduksi.length > 0">
      <div class="card">
        <div class="card-body">
          <button type="button" class="btn btn-primary mb-2" data-bs-toggle="modal" data-bs-target="#modal-tambah-produksi-user">+ Tambah</button>
          <komponen-tambah-produksi-user :uuid="dataMe.uuid" @get-data-produksi="getDataProduksi" />
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="min-width: 900px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Jenis Produk</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Jumlah</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Tangal Produksi</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Status</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.id">
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ index + 1 }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ item.produk.nama }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ item.jumlah }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ getTanggalID(item.tanggal) }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    <div v-if="item.setujui">
                      <span class="badge bg-success"> <i class="bi bi-check2-circle"></i> Valid</span>
                    </div>
                    <div v-else>
                      <span class="badge bg-warning"> <i class="bi bi-check2-circle"></i> In valid</span>
                    </div>
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    <div class="btn-group" role="group" aria-label="Basic mixed styles example">
                      <button :disabled="item.setujui" @click="deleteProduksiById(item.id)" type="button" class="btn btn-danger"><i class="bi bi-trash3-fill"></i></button>
                      <button :disabled="item.setujui" @click="getDataProduksiById(item.id)" data-bs-toggle="modal" data-bs-target="#modal-edit-produksi-user" type="button" class="btn btn-warning">
                        <i class="bi bi-pencil-square"></i>
                      </button>
                    </div>
                    <komponen-edit-produksi-user :data-produksi="dataProduksiById" @get-data-produksi="getDataProduksi" />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataProduksi" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<style>
.merah {
  background-color: rgb(255, 198, 198);
}
td.green {
  background: rgb(167, 255, 176);
}
td.yellow {
  background: rgb(228, 255, 137);
}
</style>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenTambahProduksiUser from '@/components/KomponenTambahProduksiUser.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';
import router from '@/router';
import { DateTime } from 'luxon';
import KomponenEditProduksiUser from '@/components/KomponenEditProduksiUser.vue';

export default {
  name: 'pageDataProduksi',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
    KomponenTambahProduksiUser,
    KomponenEditProduksiUser,
  },
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      dataProduksi: [],
      dataProduksiById: {},
      dataPagination: [],
      namaPage: 'Data Produksi',
      uuid: '',
    };
  },
  props: {
    searchResults: {
      required: true,
    },
    dataMe: {
      immediate: Object,
      required: false,
    },
  },
  watch: {
    searchResults: 'getDataProduksi',
    dataMe: 'getDataProduksi',
  },

  methods: {
    getTanggalID(value) {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.fromISO(value, { zone: wibTimezone }).setLocale('id');
      const formattedTime = currentTime.toFormat('dd MMMM yyyy');
      return formattedTime;
    },
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(value) {
      if (value % 2 == 0) {
        return 'background-color:#d0d2ff';
      } else {
        return 'background-color:#eff1ff';
      }
    },
    class_td() {
      return 'align-middle text-center';
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getDataProduksiById(id) {
      axios
        .get(`${API_KEY}produksi/${id}`)
        .then((response) => {
          this.dataProduksiById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    deleteProduksiById(id) {
      SweetAlertHelper.confirm('Apakah yakin?', 'Data akan dihapus permanen.').then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${API_KEY}produksi_hapus/${id}`)
            .then((response) => {
              if (response.status === 204) {
                SweetAlertHelper.success('Berhasil!', 'Data telah dihapus.');
                this.getDataProduksi();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    getDataProduksi() {
      axios
        .get(`${API_KEY}produksi_get_all_by_user/${this.dataMe ? this.dataMe.uuid : ''}`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataProduksi = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataProduksi();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
