<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination mx-4">
      <li class="page-item" :class="{ disabled: currentPage === 1 }">
        <a class="page-link" href="#" aria-label="Previous" @click.prevent="previousPage">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item" v-for="page in displayedPageNumbers" :key="page" :class="{ active: currentPage === page }" style="z-index: 1">
        <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
      </li>
      <li class="page-item" :class="{ disabled: currentPage === totalPages }">
        <a class="page-link" href="#" aria-label="Next" @click.prevent="nextPage">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      currentPage: 1,
      itemsPerPage: 15,
      maxDisplayedPages: 3,
      dataPrev: JSON.stringify(this.data),
      previousData: [],
    };
  },
  props: {
    data: {
      type: Object,
      require: true,
    },
  },
  watch: {
    data(newData) {
      this.previousData = JSON.parse(JSON.stringify(newData));
      this.emitDataToParent();
    },
  },
  mounted() {
    this.previousData = JSON.parse(JSON.stringify(this.data));
    this.emitDataToParent();
  },
  methods: {
    emitDataToParent() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      const slicedData = this.previousData.slice(startIndex, endIndex);
      this.$emit('update:data', slicedData);
    },
    previousPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.emitDataToParent();
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.emitDataToParent();
      }
    },
    goToPage(page) {
      this.currentPage = page;
      this.emitDataToParent();
    },
  },
  computed: {
    totalPages() {
      return Math.ceil(this.previousData.length / this.itemsPerPage);
    },
    displayedPageNumbers() {
      const totalPages = this.totalPages;
      const currentPage = this.currentPage;
      const maxDisplayedPages = this.maxDisplayedPages;

      if (totalPages <= maxDisplayedPages) {
        return Array.from({ length: totalPages }, (_, i) => i + 1);
      } else {
        const halfMaxDisplayedPages = Math.floor(maxDisplayedPages / 2);
        const firstDisplayedPage = Math.max(currentPage - halfMaxDisplayedPages, 1);
        const lastDisplayedPage = Math.min(firstDisplayedPage + maxDisplayedPages - 1, totalPages);
        return Array.from({ length: lastDisplayedPage - firstDisplayedPage + 1 }, (_, i) => firstDisplayedPage + i);
      }
    },
  },
};
</script>
