<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card">
        <div class="card-body">
          <div class="text-end">
            <button type="button" class="btn btn-outline-secondary px-4">{{ currentTime ? currentTime : '00.00.00' }}</button>
            <button type="button" @click="pulihkanSemua" class="btn btn-outline-secondary px-4 mx-2">Pulihkan Semua</button>
          </div>
        </div>
        <div class="card-body py-3">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <div class="row">
              <div class="col-md-6 m-0" v-for="item in dataKaryawan" :key="item.id">
                <div class="card">
                  <div class="card-body">
                    <div class="d-flex">
                      <div class="overflow-hidden" style="height: 120px; width: 150px">
                        <img style="width: 100%; min-height: 100%" :src="getUrl(item.doc.foto)" :alt="item.doc.foto ? 'Foto' : 'Foto Tidak Tersedia'" />
                      </div>
                      <div class="px-3 justify-content-between" style="width: 100%">
                        <div>
                          <h4 class="card-title my-0 py-2">{{ item.nama }}</h4>
                          <h6 class="">{{ item.no_nik }}</h6>
                          <h6 class="">{{ item.jenis_kelamin }}</h6>
                        </div>
                        <div class="d-flex justify-content-between align-items-center" v-if="item.absensi.length !== 0">
                          <div>
                            <h5 class="py-0 my-0 text-danger">Tanggal : {{ item.absensi[0].tanggal }}</h5>
                            <h5 class="py-0 my-0 text-danger">Jam Mulai : {{ item.absensi[0].time_start }}</h5>
                          </div>
                        </div>
                        <div v-else><h5 class="text-start text-danger">Tidak Masuk</h5></div>
                        <div class="text-end">
                          <button type="button" @click="pulihkan(item.uuid)" class="btn btn-outline-success px-4">Pulihkan</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { DateTime } from 'luxon';
import KomponenJudul from '@/components/KomponenJudul.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenNotFound from '@/components/KomponenNotFound.vue';
import { API_KEY } from '../../env';
import router from '@/router';

export default {
  name: 'pagePemulihan',
  components: {
    KomponenJudul,
    KomponenNotFound,
  },
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      dataKaryawan: [],
      index: 0,
      namaPage: 'Pemulihan',
      intervalIds: {},
      currentTime: '',
      statis: {},
      shift: false,
    };
  },
  props: {
    searchResults: { required: true },
  },

  watch: {
    searchResults: 'getKaryawanPemulihan',
  },

  methods: {
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
    getKaryawanPemulihan() {
      axios
        .get(`${API_KEY}karyawan_get_pemulihan`, {
          params: {
            search_query: this.searchResults,
            tanggal: this.getDateWIB(),
            shift: this.statis ? this.statis.shift : 'Normal',
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    pulihkanSemua() {
      SweetAlertHelper.confirm('Yakin dipulihkan semua?', 'Jika yakin klik oke!').then((result) => {
        if (result.isConfirmed) {
          axios
            .patch(`${API_KEY}karyawan_pulihkan_semua/${this.getDateWIB()}`)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
              this.getKaryawanPemulihan();
            })
            .catch((error) => {
              if (error.response && error.response.status === 409) {
                SweetAlertHelper.error('Gagal!', error.response.data.msg);
              } else if (error.response && error.response.status === 400) {
                router.push({ name: 'Eror', params: { msg: error } });
              }
            });
        }
      });
    },

    pulihkan(uuid) {
      SweetAlertHelper.confirm('Apakah yakin dipulihkan?', 'Jika yakin klik oke!').then((result) => {
        if (result.isConfirmed) {
          axios
            .patch(`${API_KEY}karyawan_pulihkan_by_uuid/${uuid}`)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
              this.getKaryawanPemulihan();
            })
            .catch((error) => {
              if (error.response && error.response.status === 409) {
                SweetAlertHelper.error('Gagal!', error.response.data.msg);
              } else if (error.response && error.response.status === 400) {
                router.push({ name: 'Eror', params: { msg: error } });
              }
            });
        }
      });
    },

    updateTime() {
      const options = {
        timeZone: 'Asia/Jakarta',
        hour12: false,
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
      };
      this.currentTime = new Date().toLocaleTimeString('id-ID', options);
    },

    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getStatisAktif() {
      axios
        .get(`${API_KEY}statis_get_aktif`)
        .then((response) => {
          this.statis = response.data.data;
          this.shift = this.statis.shift === 'Normal';
        })
        .then(() => {
          this.getKaryawanPemulihan();
        })
        .catch(() => {
          router.push({ name: 'Eror', params: { msg: 'error' } });
        });
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    setInterval(this.updateTime, 1000);
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDateWIB();
      this.getStatisAktif();
    } else {
      router.push('/login');
    }
  },
};
</script>
