<template>
  <main id="main" class="main pt-4 mt-4">
    <komponen-judul class="m-4" :title="namaPage" />
    <section class="section profile">
      <div class="col-lg-5 mx-auto">
        <div class="card p-2">
          <komponen-google-lokasi :reload-lokasi="randomString" @update-form-lokasi="updateFormLokasi" @data-profil="dataProfil" />
          <div class="d-flex justify-content-center align-items-end pt-2" ref="contentToExport">
            <div class="card-body text-center">
              <p class="text-warning fst-italic" style="font-size: 12px">Absensi {{ dataProfilById && dataProfilById.min_efektif_start ? dataProfilById.min_efektif_start : 0 }} Menit sebelum waktu mulai akan mendapatkan Bonus</p>
              <div class="mb-4">
                <h1 class="text-white" style="font-weight: 600">~ {{ currentTime }} ~</h1>
                <p class="text-white fs-6" style="margin-top: -15px">{{ getTanggalID() }}</p>
              </div>
              <div class="card bg-light p-4">
                <div class="text-center">
                  <div class="mb-4">
                    <h3 class="text-primary" style="font-weight: 600">{{ dataMe.nama }}</h3>
                    <h6 style="margin-top: -5px">~ {{ dataMe ? dataMe.jabatan.nama : '' }} ~</h6>
                  </div>
                  <div>
                    <p>Sift {{ statis.shift }}</p>
                    <h3 style="font-weight: 900; margin-top: -15px">( {{ statis.jam_start }} - {{ statis.jam_end }} )</h3>
                  </div>
                  <div class="d-flex justify-content-evenly">
                    <div style="font-size: 12px" class="me-1">
                      <p style="font-weight: 800">Lintang</p>
                      <p style="margin-top: -15px">~{{ formData.lat }}~</p>
                    </div>
                    <div style="font-size: 12px" class="ms-1">
                      <p style="font-weight: 800">Bujur</p>
                      <p style="margin-top: -15px">~{{ formData.lng }}~</p>
                    </div>
                  </div>
                  <hr />
                  <div>
                    <button @click="refreshLokasi" type="button" class="btn btn-md btn-primary w-100">Refresh</button>
                    <div class="d-flex justify-content-between p-0 m-0">
                      <button
                        :disabled="dataAbsensiLastByUser ? (dataAbsensiLastByUser.time_end === null ? true : false) : false"
                        @click="checkIn"
                        type="button"
                        style="width: 48%"
                        :class="['btn', dataAbsensiLastByUser && dataAbsensiLastByUser.time_end === null ? 'btn-secondary' : 'btn-success', 'btn-sm', 'my-3', 'mx-1', 'px-2']"
                      >
                        <i class="bi bi-skip-start-circle"></i> Check In
                      </button>
                      <button
                        :disabled="dataAbsensiLastByUser && dataAbsensiLastByUser.time_end === null ? false : true"
                        @click="checkOut"
                        type="button"
                        style="width: 48%"
                        :class="['btn', dataAbsensiLastByUser && dataAbsensiLastByUser.time_end === null ? 'btn-danger' : 'btn-secondary', 'btn-sm', 'my-3', 'mx-1', 'px-2']"
                      >
                        <i class="bi bi-stop-circle"></i> Check Out
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<style>
.custom-car {
  background-image: url('../../public/img/kartu.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}
</style>

<script>
import axios from 'axios';
import router from '@/router';
import { API_KEY } from '../../env';
import { DateTime } from 'luxon';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenJudul from '@/components/KomponenJudul.vue';
import KomponenGoogleLokasi from '@/components/KomponenGoogleLokasi.vue';

export default {
  name: 'pagePresensi',
  components: {
    KomponenGoogleLokasi,
    KomponenJudul,
  },
  data() {
    return {
      namaPage: 'Presensi',
      loading: false,
      dataMe: '',
      formData: {
        lat: 0,
        lng: 0,
      },
      statis: {},
      dataAbsensiLastByUser: {},
      dataProfilById: {},
      currentTime: '',
      randomString: '',
    };
  },

  methods: {
    checkIn() {
      const statis_start = DateTime.fromFormat(this.statis.jam_start, 'HH:mm:ss');
      const start = DateTime.fromFormat(this.getTimeWIB(), 'HH:mm:ss');
      const min_efektif = DateTime.fromFormat('00:00:00', 'HH:mm:ss').plus({ minutes: this.statis.min_efektif_start });
      let data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: this.dataMe.uuid,
        tanggal: this.getDateWIB(),
        hadir: 'Hadir',
        keterangan: 'Hadir',
        time_start: statis_start - start > min_efektif ? this.statis.jam_start : statis_start > start ? this.statis.jam_start : this.getTimeWIB(),
        time_end: null,
        shift: this.statis.shift,
        lat: this.formData.lat,
        lng: this.formData.lng,
        bonus: statis_start - start > min_efektif ? true : false,
        nominal_bonus: this.dataMe.nominal_bonus,
        nominal_gaji: this.dataMe.gaji,
        kemampuan: this.dataMe.kemampuan.nama,
        jabatan: this.dataMe.jabatan.nama,
        penjab: this.dataMe.nama,
      };
      SweetAlertHelper.confirm('Yakin mulai?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          axios
            .post(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_presensi_check_in' : 'absensi_presensi_check_in_no_lokasi'}`, data)
            .then((response) => {
              if (response.status === 201) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
            })
            .then(() => {
              this.getMe();
            })
            .catch((error) => {
              if (error.response.status === 403) {
                SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
              }
            });
        }
      });
    },
    checkOut() {
      const data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: this.dataMe.uuid,
        lat: this.formData.lat,
        lng: this.formData.lng,
        time_end: this.getTimeWIB(),
      };

      SweetAlertHelper.confirm('Yakin diakhiri?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          axios
            .patch(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_presensi_check_out' : 'absensi_presensi_check_out_no_lokasi'}absensi_presensi_check_out/${this.dataAbsensiLastByUser.id}`, data)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
            })
            .then(() => {
              this.getMe();
            })
            .catch((error) => {
              if (error.response.status === 403) {
                SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
              }
            });
        }
      });
    },
    getAbsensiLast() {
      axios
        .get(`${API_KEY}absensi_last_by_user/${this.dataMe.uuid}`, {
          params: {
            shift: this.statis.shift,
            tanggal: this.getDateWIB(),
          },
        })
        .then((response) => {
          this.dataAbsensiLastByUser = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateTime() {
      const options = {
        timeZone: 'Asia/Jakarta',
        hour12: false,
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
      };
      this.currentTime = new Date().toLocaleTimeString('id-ID', options);
    },
    getStatisAktif() {
      axios
        .get(`${API_KEY}statis_get_aktif`)
        .then((response) => {
          this.statis = response.data.data;
        })
        .catch(() => {
          router.push({ name: 'Eror', params: { msg: 'error' } });
        });
    },
    getMe() {
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .then(() => {
          this.getAbsensiLast();
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getTanggalID() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone).setLocale('id');
      const formattedTime = currentTime.toFormat('EEEE, dd MMMM yyyy');

      return formattedTime;
    },
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getTimeWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone);
      return currentTime.toFormat('HH:mm:ss');
    },
    updateFormLokasi(value) {
      this.formData = value;
    },
    refreshLokasi() {
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 8; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
      }
      this.randomString = result;
    },
    dataProfil(value) {
      this.dataProfilById = value;
    },
  },
  mounted() {
    setInterval(() => {
      this.updateTime();
    }, 1000);

    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getStatisAktif();
      this.getMe();
      this.getTimeWIB();

      this.getDateWIB();
      this.getTanggalID();
    } else {
      router.push('/login');
    }
  },
};
</script>
