<template>
  <div class="modal fade text-dark" id="modal-edit-absensi" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Edit Presensi</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4">
            <div class="col-md-12">
              <label for="nama_kryn" class="form-label">Nama Karyawan</label>
              <input v-model="formData.nama" type="text" class="form-control" id="tgl_produksi" />
            </div>
            <div class="col-md-12">
              <label for="add_jam_mulai" class="pb-2">Jam Mulai</label>
              <input v-model="formData.time_start" type="time" class="form-control" id="add_jam_mulai" />
            </div>
            <div class="col-md-12">
              <label for="add_jam_selesai" class="pb-2">Jam Selesai</label>
              <input v-model="formData.time_end" type="time" class="form-control" id="add_jam_selesai" />
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="simpanData(formData.id)" class="btn btn-primary" data-bs-dismiss="">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';

export default {
  name: 'KomponenTambahAbsensi',
  data() {
    return {
      formData: {
        id: 0,
        nama: '',
        time_start: '',
        time_end: '',
      },
      errResponse: {
        err_required: '',
      },
      loading: true,
    };
  },
  props: {
    dataAbsensi: {
      type: Object,
      required: true,
    },
  },
  watch: {
    dataAbsensi: {
      immediate: true,
      handler(newData) {
        this.formData.id = newData.id;
        this.formData.time_start = newData.time_start;
        this.formData.time_end = newData.time_end;
        this.formData.nama = newData.karyawan ? newData.karyawan.nama : '';
      },
    },
  },
  methods: {
    clearForm() {
      this.formData = {
        time_start: '',
        time_end: '',
      };
      this.errResponse.err_required = '';
    },
    simpanData(id) {
      this.loading = true;
      axios
        .patch(`${API_KEY}absensi_update/${id}`, this.formData)
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
          }
        })
        .then(() => {
          router.push({ name: 'pageDataAbsensi', params: { msg: 'Sukses' } });
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
};
</script>
