<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card">
        <div class="card-body">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="font-size: 14px; min-width: 1200px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" class="text-center">No.</th>
                  <th scope="col" class="text-center">Profile</th>
                  <th scope="col" class="text-center">Nama</th>
                  <th scope="col" class="text-center">Jenis Kelamin</th>
                  <th scope="col" class="text-center">Jabatan</th>
                  <th scope="col" class="text-center">Lokasi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td class="align-middle text-center">
                    {{ index + 1 }}
                  </td>
                  <td class="align-middle text-center">
                    <img style="height: 40px; width: 40px" alt="foto" :src="item.doc === null ? 'Tidak ada foto' : getUrl(item.doc.foto)" />
                  </td>
                  <td class="align-middle">
                    {{ item.nama }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.jk }}
                  </td>
                  <td class="align-middle text-center">
                    {{ item.jabatan.nama }}
                  </td>
                  <td class="align-middle text-end">
                    <div class="d-flex" style="min-width: 100%">
                      <div style="width: 80%">
                        <div v-for="(lok, lokasiIndex) in item.lokasi" :key="lokasiIndex">
                          <div class="btn-group" style="min-width: 100%" role="group" aria-label="Basic outlined example">
                            <button style="font-size: 8px; width: 20%" disabled class="btn btn-white border border-danger fw-bold">{{ lok.nama }}</button>
                            <button style="font-size: 8px; width: 30%" disabled class="btn btn-white border border-danger fw-bold">Max Radius : {{ lok.max_jarak }} m.</button>
                            <button type="button" style="font-size: 8px; width: 25%" v-if="lok.aktif" class="btn btn-danger border border-white">Aktif</button>
                            <button @click="setAktifLokasi(lok.id, item.uuid)" type="button" style="font-size: 8px; width: 25%" v-if="!lok.aktif" class="btn btn-warning border border-white">Set Aktif</button>
                            <button @click="setLokasiByIdToLocal(lok.id, lok.uuid_karyawan, lok.nama, lok.alamat, lok.latitude, lok.longitude, lok.max_jarak)" style="font-size: 8px" class="btn btn-success rounded-0 border border-white">
                              Edit
                            </button>
                            <button @click="hapusById(lok.id)" style="font-size: 8px" class="btn btn-danger rounded-0 border border-white">Hapus</button>
                          </div>
                        </div>
                      </div>
                      <div style="width: 20%">
                        <button @click="setUuidToLocal(item.uuid, item.nama, item.jabatan.nama)" style="width: 100%; height: 100%" class="btn btn-primary btn-xl mr-1 rounded-start-0 border border-white">
                          <i class="bi bi-plus-circle"></i>
                        </button>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import router from '@/router';
import { API_KEY } from '../../env';

export default {
  name: 'pageKaryawanLokasi',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
  },

  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      namaPage: 'Lokasi',
      dataKaryawan: [],
      dataPagination: [],
      hoverIndex: null,
    };
  },

  props: {
    searchResults: {
      required: true,
    },
  },

  watch: {
    searchResults: 'getKaryawanLokasi',
  },
  methods: {
    setAktifLokasi(id, uuid_karyawan) {
      SweetAlertHelper.confirm('Yakin aktifkan lokasi?', 'Data akan diaktifkan.').then((result) => {
        if (result.isConfirmed) {
          axios
            .patch(`${API_KEY}lokasi_set_aktif`, {
              id: id,
              uuid_karyawan: uuid_karyawan,
            })
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', 'Lokasi telah diaktifkan.');
                this.getKaryawanLokasi();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    hapusById(id) {
      SweetAlertHelper.confirm('Yakin dihapus?', 'Data akan dihapus permanen!').then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${API_KEY}lokasi_hapus_by_id/${id}`)
            .then((response) => {
              if (response.status === 204) {
                SweetAlertHelper.success('Berhasil!', 'Lokasi telah dihapus.');
                this.getKaryawanLokasi();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    setUuidToLocal(uuid, nama, jabatan) {
      router.push({
        name: 'KomponenTambahLokasi',
      });
      const data = { uuid, nama, jabatan };
      localStorage.setItem('uuid', JSON.stringify(data));
    },
    setLokasiByIdToLocal(id, uuid_karyawan, nama, alamat, latitude, longitude, max_jarak) {
      router.push({
        name: 'KomponenEditLokasi',
      });
      const data = { id, uuid_karyawan, nama, alamat, latitude, longitude, max_jarak };
      localStorage.setItem('lokasi_by_id', JSON.stringify(data));
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    getKaryawanLokasi() {
      axios
        .get(`${API_KEY}lokasi_get_all_karyawan_lokasi`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getKaryawanLokasi();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
