<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <komponen-spinner v-if="loading" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="row">
        <div class="card p-2 position-sticky" style="top: 60px; z-index: 2">
          <komponen-google-lokasi :reload-lokasi="randomString" @update-form-lokasi="updateFormLokasi" @data-profil="dataProfil" />
          <div class="d-flex justify-content-between align-items-middle mt-2 overflow-auto">
            <div class="btn-group" role="group" aria-label="Basic outlined example" style="min-width: 700px">
              <button type="button" class="btn btn-outline-secondary">Mulai : {{ statis.jam_start }}</button>
              <button type="button" class="btn btn-outline-secondary">Selesai : {{ statis.jam_end }}</button>
              <button type="button" class="btn btn-outline-secondary">Shift : {{ statis.shift }}</button>
              <button type="button" class="btn btn-outline-secondary">Waktu : {{ currentTime }}</button>
              <button type="button" @click="refreshLokasi" class="btn btn-outline-secondary">Refresh Lokasi</button>
            </div>
            <div class="text-end">
              <button type="button" @click="akhiri" class="btn btn-outline-secondary px-4">Akhiri</button>
            </div>
          </div>
        </div>
        <div class="card overflow-auto" style="margin-top: -20px">
          <div class="card-body py-3 px-0" style="min-height: 500px">
            <div class="row">
              <div class="col-md-6 m-0" v-for="item in dataKaryawan" :key="item.id">
                <div :class="[item.hadir == 'Tidak Hadir' ? 'p-0 alert alert-danger' : item.hadir == 'Izin' ? 'p-0 alert alert-success' : 'card']">
                  <div class="card-body overflow-auto">
                    <div class="d-flex" style="min-width: 450px">
                      <div style="height: 140px; width: 140px">
                        <img style="width: 140px" :src="getUrl(item.doc.foto)" :alt="item.doc.foto ? 'Foto' : 'Foto Tidak Tersedia'" />
                      </div>
                      <div class="px-2 justify-content-between" style="width: 100%">
                        <div>
                          <h4 class="card-title my-0 py-2">{{ item.nama }}</h4>
                          <h6 class="">{{ item.no_nik }}</h6>
                          <h6 class="">{{ item.jk }}</h6>
                        </div>
                        <div class="d-flex justify-content-between align-items-center" v-if="item.hadir == 'Hadir'">
                          <div>
                            <p class="py-0 my-0 text-success fw-semibold" style="font-size: 14px">Jam Mulai : {{ item.jam_mulai }}</p>
                            <p :hidden="item.absensi.length > 0 && item.absensi[0].time_end === null" class="py-0 mt-0 my-2 text-danger fw-semibold">Durasi : {{ item.durasi ? item.durasi : '00:00:00' }}</p>
                            <p :hidden="item.absensi.length > 0 && item.absensi[0].time_end !== null" class="py-0 mt-0 my-2 text-primary fw-semibold" style="font-size: 14px">
                              Durasi : {{ kalkukasiDurasi(item.durasi ? item.durasi : '00:00:00', item.durasi_mulai) }}
                            </p>
                          </div>
                          <div>
                            <button
                              :disabled="item.absensi.length > 0 && item.absensi[0].time_end === null"
                              @click="checkIn(item.uuid, item.nominal_bonus, item.nominal_gaji, item.kemampuan.nama, item.jabatan.nama)"
                              type="button"
                              :class="['btn', item.absensi.length > 0 && item.absensi[0].time_end === null ? 'btn-secondary' : 'btn-success', 'btn-sm', 'my-3', 'mx-1', 'px-2']"
                            >
                              <i class="bi bi-skip-start-circle"></i> Start
                            </button>
                            <button
                              :disabled="item.absensi.length === 0 || item.absensi[0].time_end !== null"
                              @click="checkOut(item.uuid, item.absensi[0].id)"
                              type="button"
                              :class="['btn', item.absensi.length === 0 || item.absensi[0].time_end !== null ? 'btn-secondary' : 'btn-danger', 'btn-sm', 'my-3', 'mx-1', 'px-2']"
                            >
                              <i class="bi bi-stop-circle"></i> Stop
                            </button>
                          </div>
                        </div>
                        <div v-else>
                          <h2 :class="['text-end', item.hadir == 'Izin' ? 'text-success' : item.hadir == 'Tidak Hadir' ? 'text-danger' : '', 'fw-bold']">{{ item.hadir }}</h2>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<style>
.bg-custom {
  background-color: #ff397b;
}
</style>

<script>
import axios from 'axios';
import KomponenJudul from '@/components/KomponenJudul.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import KomponenNotFound from '@/components/KomponenNotFound.vue';
import KomponenSpinner from '@/components/KomponenSpinner.vue';
import router from '@/router';
import { DateTime } from 'luxon';
import { API_KEY } from '../../env';
import KomponenGoogleLokasi from '@/components/KomponenGoogleLokasi.vue';

export default {
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenSpinner,
    KomponenGoogleLokasi,
  },
  data() {
    return {
      namaPage: 'Check Out',
      loading: false,
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      msg: '',
      currentTime: '',
      dataMe: {},
      statis: {},
      dataProfilById: {},
      shift: '',
      dataKaryawan: [],
      formData: {
        lat: 0,
        lng: 0,
      },
      elapsedTime: 0,
      jamIni: 0,
      menitIni: 0,
      detikIni: 0,
      errLokasi: '',
      randomString: '',
    };
  },
  props: {
    searchResults: {
      required: false,
    },
  },

  watch: {
    searchResults: 'getKaryawanCheckOut',
  },
  methods: {
    getJamEfektif() {
      this.loading = true;
      axios
        .get(`${API_KEY}statis_get_aktif`)
        .then((response) => {
          this.statis = response.data.data;
          this.getKaryawanCheckOut(this.statis.shift);
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
    getKaryawanCheckOut(shift) {
      this.loading = true;
      axios
        .get(`${API_KEY}karyawan_get_check_out`, {
          params: {
            search_query: this.searchResults,
            tanggal: this.getDateWIB(),
            nama_shift: shift,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    checkIn(uuid_karyawan, nominal_bonus, nominal_gaji, kemampuan, jabatan) {
      const data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: uuid_karyawan,
        tanggal: this.getDateWIB(),
        hadir: 'Hadir',
        keterangan: 'Hadir',
        time_start: this.getTimeWIB(),
        time_end: null,
        shift: this.statis.shift,
        lat: this.formData.lat,
        lng: this.formData.lng,
        bonus: false,
        nominal_bonus: nominal_bonus,
        nominal_gaji: nominal_gaji,
        kemampuan: kemampuan,
        jabatan: jabatan,
        penjab: this.dataMe.nama,
      };

      SweetAlertHelper.confirm('Yakin mulai?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          this.loading = true;
          axios
            .post(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_presensi_check_in' : 'absensi_presensi_check_in_no_lokasi'}`, data)
            .then((response) => {
              if (response.status === 201) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
            })
            .then(() => {
              this.getJamEfektif();
            })
            .catch((error) => {
              if (error.response.status === 403) {
                SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
              }
            })
            .finally(() => {
              this.loading = false;
            });
        }
      });
    },
    checkOut(uuid_karyawan, id) {
      const data = {
        uuid_user: this.dataMe.uuid,
        uuid_karyawan: uuid_karyawan,
        lat: this.formData.lat,
        lng: this.formData.lng,
        time_end: this.getTimeWIB(),
      };
      SweetAlertHelper.confirm('Yakin berhenti?', 'Klik oke jika iya!').then((result) => {
        if (result.isConfirmed) {
          this.loading = true;
          axios
            .patch(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_presensi_check_out' : 'absensi_presensi_check_out_no_lokasi'}/${id}`, data)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
            })
            .then(() => {
              this.getJamEfektif();
            })
            .catch((error) => {
              if (error.response.status === 403) {
                SweetAlertHelper.error('gagal', 'Anda berada diluar radius lokasi.');
              }
            })
            .finally(() => {
              this.loading = false;
            });
        }
      });
    },
    akhiri() {
      const data = {
        uuid_user: this.dataMe.uuid,
        lat: this.formData.lat,
        lng: this.formData.lng,
        time_end: this.getTimeWIB(),
        tanggal: this.getDateWIB(),
      };
      SweetAlertHelper.confirm('Apakah yakin mengakhiri?', 'Jika yakin klik oke!').then((result) => {
        if (result.isConfirmed) {
          this.loading = true;
          axios
            .patch(`${API_KEY}${this.dataProfilById.lokasi == true ? 'absensi_selesai' : 'absensi_selesai_no_lokasi'}`, data)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
              }
              this.getKaryawanCheckOut();
            })
            .catch((error) => {
              if (error.response && error.response.status === 409) {
                SweetAlertHelper.error('Gagal!', error.response.data.msg);
              } else if (error.response && error.response.status === 400) {
                router.push({ name: 'Eror', params: { msg: error } });
              } else if (error.response && error.response.status === 403) {
                SweetAlertHelper.error('Gagal!', error.response.data.msg);
              }
            })
            .finally(() => {
              this.loading = false;
            });
        }
      });
    },
    updateTime() {
      const options = {
        timeZone: 'Asia/Jakarta',
        hour12: false,
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
      };
      this.currentTime = new Date().toLocaleTimeString('id-ID', options);

      let now = new Date();
      this.jamIni = now.getHours();
      this.menitIni = now.getMinutes();
      this.detikIni = now.getSeconds();
    },
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getTimeWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone);
      return currentTime.toFormat('HH:mm:ss');
    },
    getMe() {
      this.loading = true;
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    pad(nilai) {
      return (nilai < 10 ? '0' : '') + nilai;
    },
    kalkukasiDurasi(duasiAwal, waktuMulai) {
      let pisahJamMulai = waktuMulai.split(':');
      let selisihBerjalan = (this.jamIni - parseInt(pisahJamMulai[0])) * 3600 + (this.menitIni - parseInt(pisahJamMulai[1])) * 60 + (this.detikIni - parseInt(pisahJamMulai[2]));

      let pisahDurasiAwal = duasiAwal.split(':');
      let durasiDetik = parseInt(pisahDurasiAwal[0]) * 3600 + parseInt(pisahDurasiAwal[1]) * 60 + parseInt(pisahDurasiAwal[2]);
      let totalDetik = selisihBerjalan + durasiDetik;
      let detik = totalDetik % 60;
      let menit = Math.floor((totalDetik / 60) % 60);
      let jam = Math.floor(totalDetik / 3600);
      return this.pad(jam) + ':' + this.pad(menit) + ':' + this.pad(detik);
    },
    updateFormLokasi(value) {
      this.formData = value;
    },
    refreshLokasi() {
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 8; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
      }
      this.randomString = result;
    },
    dataProfil(value) {
      this.dataProfilById = value;
    },
  },

  mounted() {
    setInterval(() => {
      this.updateTime();
    }, 1000);
    this.changeColorScrollbar = this.defaultColorScrollbar;

    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getJamEfektif();
      this.getDateWIB();
      this.getTimeWIB();
      this.getMe();
    } else {
      router.push('/login');
    }
  },
};
</script>
