<template>
  <main id="main-padding" class="main">
    <komponen-judul-vue :title="namaPage" />
    <komponen-spinner v-if="loading" />
    <section class="section dashboard">
      <div class="row">
        <div class="col-lg-8">
          <div class="card p-4">
            <canvas class="m-2" ref="persentaseBulanan" style="max-height: 400px"></canvas>
          </div>
        </div>
        <div class="col-lg-4">
          <div class="card p-4">
            <canvas class="m-3" ref="persentaseHariIni" style="max-height: 400px"></canvas>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import Chart from 'chart.js/auto';
import KomponenJudulVue from '@/components/KomponenJudul.vue';
import KomponenSpinner from '@/components/KomponenSpinner.vue';
import router from '@/router';
import axios from 'axios';
import { API_KEY } from '../../env';
import { DateTime } from 'luxon';

export default {
  name: 'pageDashboard',
  components: {
    KomponenJudulVue,
    KomponenSpinner,
  },
  data() {
    return {
      namaPage: 'Dashboard',
      loading: false,
      dataPersentaseBulanan: [],
      statusPresensiHariIni: {},
      dataMe: {},
      jabatan: '',
    };
  },

  methods: {
    getPersentaseBulanan() {
      this.loading = true;
      axios
        .get(`${API_KEY}persentase`, {
          params: {
            year: new Date().getFullYear(),
          },
        })
        .then((response) => {
          this.dataPersentaseBulanan = response.data.data;
        })
        .then(() => {
          this.createChartPersentasePertahun();
        })
        .catch((err) => {
          router.push({ name: 'Eror', params: { msg: err } });
        })
        .finally(() => {
          this.loading = false;
        });
    },

    createChartPersentasePertahun() {
      try {
        this.loading = true;
        const bulanAngka = this.dataPersentaseBulanan.map((item) => item.bulan);
        const persentaseData = this.dataPersentaseBulanan.map((item) => item.persentase);
        const bulanMap = {
          1: 'Jan',
          2: 'Feb',
          3: 'Mar',
          4: 'Apr',
          5: 'Mei',
          6: 'Jun',
          7: 'Jul',
          8: 'Agu',
          9: 'Sep',
          10: 'Okt',
          11: 'Nov',
          12: 'Des',
        };
        const bulan = bulanAngka.map((angka) => bulanMap[angka]);
        new Chart(this.$refs.persentaseBulanan, {
          type: 'bar',
          data: {
            labels: bulan,
            datasets: [
              {
                label: `Persentase kehadiran Tahun ${new Date().getFullYear()}`,
                data: persentaseData,
                borderWidth: 1,
                backgroundColor: ['rgba(255, 99, 132, 0.2)', 'rgba(255, 159, 64, 0.2)', 'rgba(255, 205, 86, 0.2)', 'rgba(75, 192, 192, 0.2)', 'rgba(54, 162, 235, 0.2)', 'rgba(153, 102, 255, 0.2)', 'rgba(201, 203, 207, 0.2)'],
                borderColor: ['rgb(255, 99, 132)', 'rgb(255, 159, 64)', 'rgb(255, 205, 86)', 'rgb(75, 192, 192)', 'rgb(54, 162, 235)', 'rgb(153, 102, 255)', 'rgb(201, 203, 207)'],
              },
            ],
          },
          options: {
            scales: {
              y: {
                max: 100,
                beginAtZero: true,
                ticks: {
                  stepSize: 10,
                  callback: function (value) {
                    return value + '%';
                  },
                },
              },
            },
          },
        });
      } catch (err) {
        console.log(err);
      } finally {
        this.loading = false;
      }
    },

    persentaseHariIni() {
      axios
        .get(`${API_KEY}persentase_hari_ini/${this.getDateWIB()}`)
        .then((response) => {
          this.statusPresensiHariIni = response.data.data;
        })
        .then(() => {
          this.createChartPersentaseHariIni();
        })
        .catch((err) => {
          router.push({ name: 'Eror', params: { msg: err } });
        })
        .finally(() => {
          this.loading = false;
        });
    },

    createChartPersentaseHariIni() {
      const totalHadir = this.statusPresensiHariIni.hadir;
      const totalTidak = this.statusPresensiHariIni.tidak_hadir;
      const totalIzin = this.statusPresensiHariIni.izin;
      const totalSemua = totalHadir + totalTidak + totalIzin;

      const persentaseHadir = (totalHadir / totalSemua) * 100;
      const persentaseTidak = (totalTidak / totalSemua) * 100;
      const persentaseIzin = (totalIzin / totalSemua) * 100;

      const bulatkanHadir = Math.round(persentaseHadir);
      const bulatkanTidak = Math.round(persentaseTidak);
      const bulatkanIzin = Math.round(persentaseIzin);

      new Chart(this.$refs.persentaseHariIni, {
        type: 'doughnut',
        data: {
          labels: ['Hadir', 'Tidak Hadir', 'Izin'],
          datasets: [
            {
              label: 'Total',
              data: [bulatkanHadir, bulatkanTidak, bulatkanIzin],
              backgroundColor: ['rgb(54, 162, 235)', 'rgb(255, 99, 132)', 'rgb(255, 99, 177)'],
              hoverOffset: 4,
            },
          ],
        },
        options: {
          plugins: {
            tooltip: {
              callbacks: {
                label: function (context) {
                  const value = context.parsed;
                  return ' ' + value.toFixed(0) + ' %';
                },
              },
            },
          },
        },
      });
    },

    getMe() {
      this.loading = true;
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
          this.jabatan = this.dataMe ? response.data.data.jabatan.nama : '';
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },

    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDateWIB();
      this.getMe();
      this.persentaseHariIni();
      this.getPersentaseBulanan();
    } else {
      router.push('/login');
    }
  },
};
</script>
