<template>
  <div class="modal fade" id="modal-tambah-absensi" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Tambah Presensi</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4">
            <div class="col-md-12">
              <label for="nama_kryn" class="form-label">Nama Karyawan</label>
              <select v-model="formData.uuid_karyawan" class="form-select" id="nama_kryn">
                <option selected>---Pilih Nama Karyawan---</option>
                <option v-for="item in dataKaryawan" :key="item.uuid" :value="item.uuid">{{ item.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="sts-hadir" class="form-label">Status Kehadiran</label>
              <select v-model="formData.hadir" class="form-select" id="sts-hadir" aria-label="Default select example">
                <option selected>---Pilih Status---</option>
                <option value="Hadir">Hadir</option>
                <option value="Izin">Izin</option>
                <option value="Tidak Hadir">Tidak hadir</option>
              </select>
            </div>
            <div class="col-md-12" :hidden="tampilKeterangan">
              <label for="tgl_produksi" class="pb-2">Alasan Izin</label>
              <input v-model="formData.keterangan" type="text" class="form-control" id="tgl_produksi" />
            </div>
            <div class="col-md-12">
              <label for="tgl_produksi" class="pb-2">Tanggal</label>
              <input v-model="formData.tanggal" type="date" class="form-control" id="tgl_produksi" />
            </div>
            <div class="col-md-12">
              <div class="d-flex align-items-middle">
                <div class="form-check form-switch mx-2 ps-1 mt-2">
                  <input id="add_shift" v-model="shift" class="form-check-input mx-1" type="checkbox" />
                  <label for="add_bonus" class="ms-3">Shift {{ formData.shift }}</label>
                </div>
                <div class="form-check form-switch mx-2 ps-1 mt-2" :hidden="tampilJam">
                  <input id="add_bonus" v-model="formData.bonus" class="form-check-input mx-1" type="checkbox" />
                  <label for="add_bonus" class="ms-3">Bonus</label>
                </div>
              </div>
            </div>
            <div class="col-md-12" :hidden="tampilJam">
              <label for="add_jam_mulai" class="pb-2">Jam Mulai</label>
              <input v-model="formData.time_start" type="time" class="form-control" id="add_jam_mulai" />
            </div>
            <div class="col-md-12" :hidden="tampilJam">
              <label for="add_jam_selesai" class="pb-2">Jam Selesai</label>
              <input v-model="formData.time_end" type="time" class="form-control" id="add_jam_selesai" />
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="simpanData" class="btn btn-primary" data-bs-dismiss="">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';
import { DateTime } from 'luxon';

export default {
  name: 'KomponenTambahAbsensi',
  data() {
    return {
      formData: {
        uuid_karyawan: '',
        tanggal: this.getDateWIB(),
        hadir: '',
        keterangan: '',
        bonus: false,
        shift: 'Normal',
        time_start: this.getTimeWIB(),
        time_end: this.getTimeWIB(),
        nominal_bonus: 0,
        nominal_gaji: 0,
        kemampuan: '',
        jabatan: '',
        penjab: '',
      },
      errResponse: {
        err_required: '',
      },
      loading: true,
      dataKaryawan: [],
      dataProduk: [],
      statis: {},
      shift: false,
      tampilKeterangan: true,
      tampilJam: true,
      dataKaryawanById: {},
      dataProfilById: {},
      dataMe: {},
    };
  },
  watch: {
    shift: function (newVal) {
      this.formData.shift = newVal ? 'Lembur' : 'Normal';
    },
    dataProfilById: function (newVal) {
      this.formData.nominal_bonus = newVal.nominal_bonus;
    },
    dataKaryawanById: function (value) {
      this.formData.jabatan = value.jabatan ? value.jabatan.nama : '';
      this.formData.kemampuan = value.kemampuan ? value.kemampuan.nama : '';
      this.formData.nominal_gaji = value.gaji ? value.gaji : 0;
    },
    dataMe: function (value) {
      this.formData.penjab = value ? value.nama : '';
    },
    'formData.uuid_karyawan': function () {
      this.getKaryawanByUuid(this.formData.uuid_karyawan);
    },
    'formData.hadir': function (item) {
      this.formData.keterangan = item == 'Hadir' ? 'Hadir' : item == 'Tidak Hadir' ? 'Alpha' : '';
      this.tampilKeterangan = item == 'Izin' ? false : true;
      this.tampilJam = item == 'Hadir' ? false : true;
      this.formData.time_end = item != 'Hadir' ? null : this.getTimeWIB();
    },
  },
  methods: {
    clearForm() {
      this.formData = {
        uuid_karyawan: '',
        tanggal: this.getDateWIB(),
      };
      this.errResponse.err_required = '';
    },
    getDataKaryawan() {
      axios
        .get(`${API_KEY}karyawan_get_all_aktif`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    tambahAbsensi() {
      this.save();
    },
    getMe() {
      const token = localStorage.getItem('accessToken');
      this.loading = true;
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },
    getTimeWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.now().setZone(wibTimezone);
      return currentTime.toFormat('HH:mm:ss');
    },
    getProfilById() {
      axios
        .get(`${API_KEY}profil/1`)
        .then((response) => {
          this.dataProfilById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getKaryawanByUuid(uuid) {
      axios
        .get(`${API_KEY}karyawan_get_by_uuid/${uuid}`)
        .then((response) => {
          this.dataKaryawanById = response.data.data;
        })
        .catch(() => {
          this.dataKaryawanById = {};
        });
    },
    simpanData() {
      this.loading = true;
      axios
        .post(`${API_KEY}absensi_tambah`, this.formData)
        .then((response) => {
          if (response.status === 201) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
          }
        })
        .then(() => {
          router.push({ name: 'pageDataAbsensi', params: { msg: 'Sukses' } });
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getMe();
      this.getDataKaryawan();
      this.getTimeWIB();
      this.getDateWIB();
      this.getProfilById();
    } else {
      router.push('/login');
    }
  },
};
</script>
