<template>
  <div class="modal fade" id="modal-tambah-produksi" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Tambah Produksi</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4">
            <div class="col-md-12">
              <label for="nama_kryn" class="form-label">Nama Karyawan</label>
              <select v-model="formData.uuid_karyawan" class="form-select" id="nama_kryn">
                <option selected>---Pilih Nama Karyawan---</option>
                <option v-for="item in dataKaryawan" :key="item.uuid" :value="item.uuid">{{ item.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="jabatan" class="form-label">Produk</label>
              <select v-model="formData.id_produk" class="form-select" id="jabatan">
                <option selected>---Pilih Jenis Produk</option>
                <option v-for="item in dataProduk" :key="item.id" :value="item.id">{{ item.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="jumlah" class="form-label">Jumlah</label>
              <input v-model="formData.jumlah" type="text" class="form-control" id="jumlah" />
            </div>
            <div class="col-md-12">
              <label for="tgl_produksi" class="pb-2">Tanggal Produksi</label>
              <input v-model="formData.tanggal" type="date" class="form-control" id="tgl_produksi" />
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="simpanData" class="btn btn-primary" data-bs-dismiss="">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';
import { DateTime } from 'luxon';

export default {
  name: 'KomponenTambahProduksi',
  data() {
    return {
      formData: {
        id_produk: '',
        uuid_karyawan: '',
        jumlah: 0,
        tanggal: this.getDateWIB(),
      },
      errResponse: {
        err_required: '',
      },
      dataKaryawan: [],
      dataProduk: [],
    };
  },
  methods: {
    getDateWIB() {
      const wibTimezone = 'Asia/Jakarta';
      const currentDateWIB = DateTime.now().setZone(wibTimezone);
      return currentDateWIB.toFormat('yyyy-MM-dd');
    },

    refresProduksi() {
      this.$emit('get-data-produksi');
    },

    simpanData() {
      if (!this.formData.uuid_karyawan || !this.formData.id_produk || !this.formData.tanggal || !this.formData.jumlah) {
        this.errResponse.err_required = 'Pastikan data diisi semua!';
      } else {
        this.errResponse.err_required = '';
        axios
          .post(`${API_KEY}produksi_tambah/`, this.formData)
          .then((response) => {
            if (response.status === 201) {
              SweetAlertHelper.success('Berhasil!', response.data.msg);
              this.clearForm();
              this.refresProduksi();
            }
          })
          .catch((error) => {
            if (error.response.status == 422) {
              SweetAlertHelper.error('Error!', error);
            } else {
              router.push({ name: 'Eror', params: { msg: error } });
            }
          });
      }
    },
    clearForm() {
      this.formData = {
        id_produk: '',
        uuid_karyawan: '',
        jumlah: 0,
        tanggal: this.getDateWIB(),
      };
      this.errResponse.err_required = '';
    },
    getDataProduk() {
      axios
        .get(`${API_KEY}produk`)
        .then((response) => {
          this.dataProduk = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getDataKaryawan() {
      axios
        .get(`${API_KEY}karyawan_get_all_aktif`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataProduk();
      this.getDataKaryawan();
    } else {
      router.push('/login');
    }
  },
};
</script>
