<template>
  <div class="modal fade" id="modal-edit-user" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Edit User</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4">
            <div class="col-md-12">
              <label for="input1" class="form-label">Nama</label>
              <input disabled v-model="formData.nama" type="text" class="form-control" id="input1" required />
            </div>
            <div class="col-md-12">
              <label for="input2" class="form-label">Email</label>
              <input v-model="formData.email" type="email" class="form-control" id="input2" @input="validateEmail" required />
              <div v-if="errResponse.err_email" class="text-danger fst-italic text-start">{{ errResponse.err_email }}</div>
            </div>
            <div class="col-md-12">
              <label for="input5" class="form-label">Password</label>
              <input v-model="formData.password" type="text" class="form-control" id="input5" @input="validatePassword" required />
              <div v-if="errResponse.err_password" class="text-danger fst-italic text-start">{{ errResponse.err_password }}</div>
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
          <button type="button" @click="updateData(formData.uuid)" class="btn btn-primary" data-bs-dismiss="modal">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';

export default {
  name: 'KomponenEditUser',
  props: {
    dataUser: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      formData: {
        password: '',
        email: '',
      },
      errResponse: {
        err_required: '',
        err_email: '',
      },
    };
  },

  watch: {
    dataUser: {
      immediate: true,
      handler(newData) {
        this.formData.uuid = newData.uuid;
        this.formData.nama = newData.nama;
        this.formData.email = newData.email;
        this.formData.password = newData.password;
      },
    },
  },

  methods: {
    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.formData.email)) {
        this.errResponse.err_email = 'Format email tidak valid.';
      } else {
        this.errResponse.err_email = '';
      }
    },
    validatePassword() {
      const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

      if (!passwordPattern.test(this.formData.password)) {
        this.errResponse.err_password = 'Password harus terdiri dari setidaknya 8 karakter, minimal 1 huruf kapital, 1 huruf kecil, 1 angka, dan 1 karakter khusus.';
      } else {
        this.errResponse.err_password = '';
      }
    },
    refresUser() {
      this.$emit('get-data-user');
    },

    updateData(uuid) {
      if (!this.formData.password) {
        this.errResponse.err_required = 'Pastikan data diisi!';
      } else {
        this.errResponse.err_required = '';
        try {
          axios
            .patch(`${API_KEY}user_update_by_uuid/${uuid}`, this.formData)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
                router.push({ name: 'pageUsers', params: { msg: response.data.msg } });
                this.refresUser();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        } catch (error) {
          router.push({ name: 'Eror', params: { msg: error } });
        }
      }
    },
    clearForm() {
      (this.formData = {
        email: '',
        password: '',
      }),
        (this.errResponse = {
          err_required: '',
          err_email: '',
          err_password: '',
        });
    },
  },
};
</script>
