<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card" id="contentToExport" ref="contentToExport">
        <div class="d-flex w-50 px-3 py-4 overflow-auto" style="min-width: 320px">
          <select v-model="selectedYear" @change="updateDates" class="form-select mx-2" id="tahun">
            <option v-for="year in years" :key="year" :value="year">{{ year }}</option>
          </select>
          <select v-model="selectedMonth" @change="updateDates" class="form-select mx-2" id="bulan">
            <option v-for="month in months" :key="month.value" :value="month.value">{{ month.name }}</option>
          </select>
        </div>
        <div class="card-body">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-bordered border-secondary table-hover" style="font-size: 11px">
              <thead>
                <tr class="my-4">
                  <th scope="col" class="text-center text-white align-middle" style="min-width: 50px; background-color: #292e8c" rowspan="2">NO</th>
                  <th scope="col" class="text-center text-white align-middle" style="min-width: 150px; background-color: #292e8c" rowspan="2">NAMA</th>
                  <th scope="col" class="text-center text-white align-middle" style="min-width: 80px; background-color: #292e8c" rowspan="2">JABATAN</th>
                  <th scope="col" class="text-center text-white align-middle" :colspan="dates.length" style="background-color: #292e8c">TANGGAL</th>
                  <th scope="col" class="text-center text-white align-middle" colspan="2" style="background-color: #292e8c">JUMLAH</th>
                </tr>
                <tr class="my-4" style="background-color: #d2d2d2">
                  <th class="text-center align-middle border-secondary border-end text-white" v-for="date in dates" :key="date" style="background-color: #292e8c">{{ formatDate(date) }}</th>
                  <th class="border-secondary border-end text-center text-white align-middle" style="background-color: #292e8c">Y</th>
                  <th class="border-secondary border-end text-center text-white align-middle" style="background-color: #292e8c">T</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td class="align-middle text-center" :style="{ 'background-color': getBackgroundColor(item.absensi, 0, index + 1) }">
                    {{ index + 1 }}
                  </td>
                  <td class="align-middle" :style="{ 'background-color': getBackgroundColor(item.absensi, 0, index + 1) }">
                    {{ item.nama }}
                  </td>
                  <td class="align-middle text-center" :style="{ 'background-color': getBackgroundColor(item.absensi, 0, index + 1) }">
                    {{ item.jabatan.nama }}
                  </td>
                  <td class="align-middle text-center" v-for="date in dates" :key="date" :style="{ 'background-color': getBackgroundColor(item.absensi, date, index + 1) }">
                    {{ getStatus(item.absensi, date) }}
                  </td>
                  <td class="align-middle text-center fw-bold" :style="{ 'background-color': getBackgroundColor(item.absensi, 0, index + 1) }">
                    {{ item.hadir }}
                  </td>
                  <td class="align-middle text-center fw-bold" :style="{ 'background-color': getBackgroundColor(item.absensi, 0, index + 1) }">
                    {{ item.tidak }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <button type="button" class="btn btn-primary d-none" ref="modalButton" data-bs-toggle="modal" data-bs-target="#modal-laporan-absensi">export laporan absensii</button>
        <export-absensi-vue :bulan="selectedMonthName" :tahun="selectedYear" :dataLaporanAbsensi="dataKaryawan" :dates="dates" />
        <div class="my-2">
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import exportAbsensiVue from '../components/exsport/export_absensi.vue';
import KomponenNotFound from '@/components/KomponenNotFound.vue';
import KomponenJudul from '@/components/KomponenJudul.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import { API_KEY } from '../../env';
import router from '@/router';

export default {
  name: 'pageLapAbsensi',
  components: {
    exportAbsensiVue,
    KomponenNotFound,
    KomponenJudul,
    KomponenPagination,
  },
  data() {
    return {
      namaPage: 'Laporan Absensi',
      selectedYear: new Date().getFullYear(),
      selectedMonth: new Date().getMonth() + 1,
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      dates: [],
      years: [],
      months: [
        { value: 1, name: 'Januari' },
        { value: 2, name: 'Februari' },
        { value: 3, name: 'Maret' },
        { value: 4, name: 'April' },
        { value: 5, name: 'Mei' },
        { value: 6, name: 'Jui' },
        { value: 7, name: 'Juli' },
        { value: 8, name: 'Augustus' },
        { value: 9, name: 'September' },
        { value: 10, name: 'Oktober' },
        { value: 11, name: 'November' },
        { value: 12, name: 'Desember' },
      ],
      dataKaryawan: [],
      dataPagination: [],
    };
  },

  props: {
    searchResults: {
      required: true,
    },
    exports_status: {
      required: true,
    },
  },

  methods: {
    formatDate(date) {
      return new Date(date).getDate();
    },

    getStatus(item, date) {
      const attendance = item.find((a) => a.tanggal.split('T')[0] === date);
      if (attendance && attendance.hadir === 'Hadir') {
        return '\u2714';
      } else if (attendance && attendance.hadir === 'Tidak Hadir') {
        return 'a';
      } else if (attendance && attendance.hadir === 'Izin') {
        return 'i';
      } else {
        return '';
      }
    },

    getBackgroundColor(item, date, value) {
      const attendance = item.find((a) => a.tanggal.split('T')[0] === date);
      if (attendance && attendance.hadir === 'Hadir') {
        return '#90ff85';
      } else if (attendance && attendance.hadir === 'Tidak Hadir') {
        return '#ff8787';
      } else if (attendance && attendance.hadir === 'Izin') {
        return '#ff8787';
      } else if (value % 2 == 0) {
        return '#d0d2ff';
      } else {
        return '#eff1ff';
      }
    },

    getDataKaryawan() {
      axios
        .get(`${API_KEY}laporan_absensi`, {
          params: {
            search_query: this.searchResults ? this.searchResults : '',
            year: this.selectedYear,
            month: this.selectedMonth,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },

    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },

    statusExp() {
      const btn = this.$refs.modalButton;
      if (this.exports_status === true) {
        btn.click();
        this.getDataKaryawan();
      }
    },

    updateDates() {
      this.dates = [];
      const startDate = new Date(this.selectedYear, this.selectedMonth - 1, 1);
      const endDate = new Date(this.selectedYear, this.selectedMonth, 0);
      const daysInMonth = (endDate - startDate) / (1000 * 60 * 60 * 24);
      for (let i = 0; i <= daysInMonth; i++) {
        const date = new Date(startDate);
        date.setDate(startDate.getDate() + i + 1);
        this.dates.push(date.toISOString().slice(0, 10));
      }
    },

    getYears() {
      const currentYear = new Date().getFullYear();
      for (let i = currentYear; i >= currentYear - 10; i--) {
        this.years.push(i);
      }
    },
  },

  computed: {
    selectedMonthName() {
      const selectedMonthObject = this.months.find((month) => month.value === this.selectedMonth);
      return selectedMonthObject ? selectedMonthObject.name : 'Not Found';
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getYears();
      this.updateDates();
      this.getDataKaryawan();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },

  watch: {
    searchResults: 'getDataKaryawan',
    selectedYear: 'getDataKaryawan',
    selectedMonth: 'getDataKaryawan',
    exports_status: 'statusExp',
  },
};
</script>
