<template>
  <section class="section register min-vh-100 d-flex flex-column align-items-center justify-content-center py-4">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-lg-4 col-md-6 d-flex flex-column align-items-center justify-content-center">
          <div class="d-flex justify-content-center py-4">
            <a href="" class="logo d-flex align-items-center w-auto">
              <img src="#" alt="" />
              <span class="d-none d-lg-block">Sinar Tanjung Group</span>
            </a>
          </div>
          <!-- End Logo -->

          <div class="card mb-3">
            <div class="card-body">
              <div class="pt-4 pb-2">
                <h5 class="card-title text-center pb-0 fs-4">Login</h5>
                <p class="text-center small">Enter your username & password to login</p>
              </div>
              <form @submit.prevent="login" class="row g-3 needs-validation" novalidate>
                <div class="col-12">
                  <label for="yourUsername" class="form-label">Email</label>
                  <div class="input-group has-validation">
                    <span class="input-group-text" id="inputGroupPrepend">@</span>
                    <input v-model="formData.email" type="email" name="email" class="form-control" @input="validateEmail" id="yourEmail" required />
                    <div class="invalid-feedback">Please enter your email.</div>
                  </div>
                  <div v-if="errResponse.err_email" class="text-danger fst-italic text-start">{{ errResponse.err_email }}</div>
                </div>

                <div class="col-12">
                  <label for="yourPassword" class="form-label">Password</label>
                  <input v-model="formData.password" type="password" name="password" class="form-control" id="yourPassword" required />
                  <div class="invalid-feedback">Please enter your password!</div>
                  <div class="text-danger fst-italic text-start">{{ errResponse.err_password }}</div>
                </div>

                <div class="col-12">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" v-model="remember" name="remember" value="true" id="rememberMe" />
                    <label class="form-check-label" for="rememberMe">Remember me</label>
                  </div>
                </div>
                <div class="col-12">
                  <button @click="login" class="btn btn-primary w-100" type="submit">Login</button>
                </div>
              </form>
            </div>
          </div>

          <div class="credits">Designed by <a href="#">STG V.00.01</a></div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import { API_KEY } from '../../env';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';

export default {
  name: 'LoginView',
  data() {
    return {
      formData: {
        email: '',
        password: '',
      },
      errResponse: {
        err_email: '',
        err_password: '',
      },
      remember: false,
    };
  },

  methods: {
    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.formData.email)) {
        this.errResponse.err_email = 'Format email tidak valid.';
      } else {
        this.errResponse.err_email = '';
      }
    },
    login() {
      axios
        .post(`${API_KEY}login`, this.formData)
        .then((response) => {
          if (response && response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            if (this.remember === true) {
              localStorage.setItem('accessToken', response.data.data.accessToken);
              router.push('/');
            } else {
              localStorage.setItem('accessToken', response.data.data.accessToken);
              router.push('/');
            }
          }
        })
        .catch((error) => {
          if (error.response) {
            if (error.response.status === 404) {
              this.errResponse.err_email = 'Email belum didaftarkan.';
            } else if (error.response.status === 401) {
              this.errResponse.err_password = 'Password salah.';
            } else {
              router.push({ name: 'Error', params: { msg: error } });
            }
          } else {
            // Handle error when 'error.response' is undefined
            console.error('Unexpected error:', error);
          }
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) router.push('/');
  },
};
</script>
