<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section profile">
      <div class="row">
        <div class="col-xl-4">
          <div class="card">
            <div class="card-body profile-card pt-4 d-flex flex-column align-items-center">
              <img :src="getUrl(dataMe ? dataMe.doc.foto : '')" alt="Profile" class="rounded-circle" />
              <h2 class="text-center">{{ dataMe.nama }}</h2>
              <h3>{{ dataMe ? dataMe.jabatan.nama : '' }}</h3>
              <div class="social-links mt-2">
                <a href="#" class="twitter"><i class="bi bi-twitter"></i></a>
                <a href="#" class="facebook"><i class="bi bi-facebook"></i></a>
                <a href="#" class="instagram"><i class="bi bi-instagram"></i></a>
                <a href="#" class="linkedin"><i class="bi bi-linkedin"></i></a>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-8">
          <div class="card">
            <div class="card-body pt-3">
              <!-- Bordered Tabs -->
              <ul class="nav nav-tabs nav-tabs-bordered">
                <li class="nav-item">
                  <button class="nav-link active" data-bs-toggle="tab" data-bs-target="#profile-edit">Profil</button>
                </li>

                <li class="nav-item">
                  <button class="nav-link" data-bs-toggle="tab" data-bs-target="#profile-overview">Biodata</button>
                </li>

                <li class="nav-item">
                  <button class="nav-link" data-bs-toggle="tab" data-bs-target="#profile-change-password">Ganti Password</button>
                </li>
              </ul>
              <div class="tab-content pt-2">
                <div class="tab-pane fade show active profile-edit pt-3" id="profile-edit">
                  <div class="row mb-3">
                    <p class="col-md-4 col-lg-3 col-form-label">Profile Image</p>
                    <div class="col-md-8 col-lg-9">
                      <img :src="getUrl(dataMe ? dataMe.doc.foto : '')" alt="Profile" />
                      <div class="pt-4">
                        <a @click="toggleShow" class="btn btn-primary btn mx-2" title="Upload new profile image"><i class="bi bi-upload"></i></a>
                        <a href="#" class="btn btn-danger btn mx-1" title="Remove my profile image"><i class="bi bi-trash"></i></a>
                      </div>
                    </div>
                  </div>
                </div>

                <div class="tab-pane fade profile-overview" id="profile-overview">
                  <h5 class="card-title">Biodata</h5>

                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Nama Lenglkap</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.nama }}</div>
                  </div>

                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Nomor NIK</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.no_nik }}</div>
                  </div>

                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Jenis Kelamin</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.jk }}</div>
                  </div>

                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Jabatan</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe ? dataMe.jabatan.nama : '' }}</div>
                  </div>

                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Tempat Tanggal Lahir</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe ? (dataMe.tmpLahir ? dataMe.tmpLahir.nama.split(' ')[1] : '') : '' }} , {{ convertLocal(dataMe ? dataMe.tgl_lhr : '') }}</div>
                  </div>
                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Alamat KTP</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe ? dataMe.alamat.nama : '' }}</div>
                  </div>
                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">No Telp</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.no_telepon }}</div>
                  </div>
                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Email</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.email }}</div>
                  </div>
                  <div class="row">
                    <div class="col-lg-3 col-md-4 label">Nomor Rekening</div>
                    <div class="col-lg-9 col-md-8">{{ dataMe.no_rekening }}</div>
                  </div>
                </div>
                <div class="tab-pane fade pt-3" id="profile-change-password">
                  <form>
                    <div class="row mb-3">
                      <label for="currentPassword" class="col-md-4 col-lg-3 col-form-label">Password Lama</label>
                      <div class="col-md-8 col-lg-9">
                        <input type="password" class="form-control" v-model="formData.password_lama" id="currentPassword" />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="newPassword" class="col-md-4 col-lg-3 col-form-label">Password Baru</label>
                      <div class="col-md-8 col-lg-9">
                        <input type="password" class="form-control" v-model="formData.password_baru" id="newPassword" @input="validatePassword" />
                        <div v-if="errResponse.err_password" class="text-danger fst-italic text-start">{{ errResponse.err_password }}</div>
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="renewPassword" class="col-md-4 col-lg-3 col-form-label">Confirmasi Password</label>
                      <div class="col-md-8 col-lg-9">
                        <input type="password" class="form-control" v-model="formData.konfirmasi_password" id="renewPassword" />
                      </div>
                    </div>
                    <div class="text-danger fst-italic text-center">{{ err_required }}</div>
                    <div class="text-center">
                      <button type="button" @click="updatePassword(dataMe.uuid)" class="btn btn-primary">Save</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <my-upload
        @crop-success="cropSuccess"
        @crop-upload-success="cropUploadSuccess"
        @crop-upload-fail="cropUploadFail"
        v-model="show"
        :method="method"
        :field="field"
        :width="200"
        :height="200"
        :maxSize="maxSize"
        :url="url"
        :lang-ext="langExt"
        :headers="headers"
        :allow-img-format="allowImgFormat"
      />
    </section>
  </main>
</template>

<style>
.custom-card {
  background-image: url('../../public/img/kartu.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: -15px;
}
</style>

<script>
import axios from 'axios';
import KomponenJudul from '@/components/KomponenJudul.vue';
import router from '@/router';
import { API_KEY } from '../../env';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import myUpload from 'vue-image-crop-upload';

export default {
  name: 'ProfilView',
  components: {
    KomponenJudul,
    'my-upload': myUpload,
  },
  data() {
    return {
      namaPage: 'Profil Saya',
      dataMe: '',
      dataMeKaryawan: {},
      jabatan: '',
      provinsi: '',
      kota: '',
      kecamatan: '',
      formData: {
        password_lama: '',
        password_baru: '',
        konfirmasi_password: '',
      },
      errResponse: {
        err_password: '',
      },
      err_required: '',
      imageLoaded: false,

      url: '',
      show: false,
      imgDataUrl: '',
    };
  },

  props: {
    searchResults: {
      required: true,
    },
    field: {
      type: String,
      default: 'file',
    },
    // unique key
    ki: {
      type: String,
      default: '0',
    },
    // shows the component
    modelValue: {
      type: Boolean,
      default: true,
    },
    // add custom headers
    headers: {
      type: Object,
      default: () => null,
    },
    // width
    width: {
      type: Number,
      default: 200,
    },
    // height
    height: {
      type: Number,
      default: 200,
    },
    // disable rotate
    noRotate: {
      type: Boolean,
      default: true,
    },
    // disable circle image
    noCircle: {
      type: Boolean,
      default: false,
    },
    // disable square image
    noSquare: {
      type: Boolean,
      default: false,
    },
    // max size
    maxSize: {
      type: Number,
      default: 2048,
    },
    // language
    langType: {
      type: String,
      default: 'id',
    },
    // language package
    langExt: {
      type: Object,
      default: () => ({
        hint: 'Klik atau seret file ke sini untuk mengunggah',
        loading: 'Mengunggah…',
        noSupported: 'Browser tidak didukung, silakan gunakan IE10+ atau browser lainnya',
        success: 'Unggahan berhasil',
        fail: 'Gagal mengunggah',
        preview: 'Pratinjau',
        btn: {
          off: 'Batal',
          close: 'Tutup',
          back: 'Kembali',
          save: 'Simpan',
        },
        error: {
          onlyImg: 'Gunakan format gambar.',
          outOfSize: 'Gambar melebihi batas ukuran: ',
          lowestPx: 'Ukuran gambar terlalu kecil. Setidaknya diharapkan: ',
        },
      }),
    },
    // image background
    imgBgc: {
      type: String,
      default: '#000000',
    },
    // allows cross domain
    withCredentials: {
      type: Boolean,
      default: false,
    },
    // upload method
    method: {
      type: String,
      default: 'PATCH',
    },
    // initial image url
    initialImgUrl: {
      type: String,
      default: '',
    },
    // allowed image format
    allowImgFormat: {
      type: Array,
      default: () => ['JPG', 'PNG'],
    },
  },

  watch: {
    searchResults: 'getMe',
    dataMe: {
      handler: 'updateUrl',
      deep: true,
    },
  },

  methods: {
    clearForm() {
      this.formData = {
        password_lama: '',
        password_baru: '',
        konfirmasi_password: '',
        err_required: '',
      };
    },
    validatePassword() {
      const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

      if (!passwordPattern.test(this.formData.password_baru)) {
        this.errResponse.err_password = 'Password harus terdiri dari setidaknya 8 karakter, minimal 1 huruf kapital, 1 huruf kecil, 1 angka, dan 1 karakter khusus.';
      } else {
        this.errResponse.err_password = '';
      }
    },
    updatePassword(uuid) {
      if (!this.formData.password_lama || !this.formData.password_baru || !this.formData.konfirmasi_password) {
        this.err_required = 'Pastikan data diisi!';
      } else if (this.formData.password_baru != this.formData.konfirmasi_password) {
        this.err_required = 'Konfirmasi password salah!';
      } else {
        axios
          .patch(`${API_KEY}user_ganti_password_by_uuid/${uuid}`, this.formData)
          .then((response) => {
            if (response.status === 200) {
              SweetAlertHelper.success('Berhasil!', response.data.msg);
              router.push({
                name: 'pageProfil',
                params: {
                  randomString: 'jSQWgc875gQL0rhI8QlNJ3iXNkfX1TBNu6ixF3sj2SMBLird8tcoGQpHIIzaoEzhJIeWu4NzMeiSF3p2TSNH7DNvCnulSxAIeF7S',
                  msg: response.data.msg,
                },
              });
              this.clearForm();
            }
          })
          .catch((error) => {
            if (error.response && error.response.status === 404) {
              this.err_required = 'Password lama tidak diketahui!';
            } else {
              router.push({ name: 'Eror', params: { msg: error } });
            }
          });
      }
    },
    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    getMe() {
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    convertLocal(dateString) {
      const dateArray = dateString.split('-');
      const dateObject = new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);
      const day = dateObject.getDate();
      const monthIndex = dateObject.getMonth();
      const year = dateObject.getFullYear();
      const monthNames = ['Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];
      const monthName = monthNames[monthIndex];
      const reversedAndFormattedDate = `${day} ${monthName} ${year}`;
      return reversedAndFormattedDate;
    },

    toggleShow() {
      this.show = !this.show;
    },
    cropSuccess(imgDataUrl) {
      this.imgDataUrl = imgDataUrl;
    },
    cropUploadSuccess(jsonData) {
      if (jsonData.code === 200) {
        this.show = !this.show;
        SweetAlertHelper.success('Berhasil!', jsonData.msg);
        this.getMe();
      }
    },
    cropUploadFail(status) {
      this.show = !this.show;
      SweetAlertHelper.error('Gagal!', status);
      this.getMe();
    },
    updateUrl() {
      this.url = `${API_KEY}doc_foto_me_by_uuid/${this.dataMe.uuid}`;
    },
  },

  created() {
    this.updateUrl();
  },
  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getMe();
    } else {
      router.push('/login');
    }
  },
};
</script>
