<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section profile">
      <div class="row">
        <div class="col-xl-4">
          <div class="card">
            <div class="card-body profile-card pt-4 d-flex flex-column align-items-center">
              <img :src="formData.logo ? getUrl(formData.logo) : getUrl('logo-kosong.jpg')" alt="Profile" />
              <h2>{{ dataProfilById.nama_perusahaan }}</h2>
              <h3>{{ dataProfilById.jenis_usaha }}</h3>
              <div class="social-links mt-2">
                <a href="#" class="twitter"><i class="bi bi-twitter"></i></a>
                <a href="#" class="facebook"><i class="bi bi-facebook"></i></a>
                <a href="#" class="instagram"><i class="bi bi-instagram"></i></a>
                <a href="#" class="linkedin"><i class="bi bi-linkedin"></i></a>
              </div>
            </div>
          </div>
        </div>

        <div class="col-xl-8">
          <div class="card">
            <div class="card-body pt-3">
              <div class="overflow-auto">
                <ul class="nav nav-tabs nav-tabs-bordered" style="min-width: 900px">
                  <li class="nav-item">
                    <button class="nav-link active" data-bs-toggle="tab" data-bs-target="#profile-edit">Profil</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#pengaturan-lokasi">Lokasi</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#produk">Produk</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#jabatan">Jabatan</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#kemampuan">Kemampuan</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#gaji">Gaji</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#shift">Shift</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#minEfektifMulai">Minimal Efektif Kehadiran</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#nominalbonus">Nominal Bonus</button>
                  </li>
                  <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#profile-settings">Pengaturan</button>
                  </li>
                </ul>
              </div>
              <div class="tab-content pt-2">
                <div class="tab-pane fade profile-edit pt-3 active show" id="profile-edit">
                  <!-- Profile Edit Form -->
                  <form>
                    <div class="row mb-3">
                      <div class="col-md-4 col-lg-3 col-form-label">Logo</div>
                      <div class="col-md-8 col-lg-9">
                        <img :src="formData.logo ? getUrl(formData.logo) : getUrl('logo-kosong.jpg')" alt="Logo" />
                        <div class="col-12 d-flex">
                          <div class="pt-4">
                            <a @click="toggleShow" class="btn btn-primary btn mx-2" title="Upload new profile image"><i class="bi bi-upload"></i></a>
                            <a href="#" class="btn btn-danger btn mx-1" title="Remove my profile image"><i class="bi bi-trash"></i></a>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input1" class="col-md-4 col-lg-3 col-form-label">Nama Instansi</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.nama_perusahaan" type="text" class="form-control" id="input1" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input2" class="col-md-4 col-lg-3 col-form-label">Name Tag</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.nama_alias" type="text" class="form-control" id="input2" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input3" class="col-md-4 col-lg-3 col-form-label">Jenis Usaha</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.jenis_usaha" type="text" class="form-control" id="input3" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="profil-perusahaan" class="col-md-4 col-lg-3 col-form-label">Profil Perusahaan</label>
                      <div class="col-md-8 col-lg-9">
                        <textarea v-model="formData.profil_perusahaan" id="profil-perusahaan" class="form-control" style="height: 300px" />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input4" class="col-md-4 col-lg-3 col-form-label">Nama Pimpinan</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.pimpinan" type="text" class="form-control" id="input4" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input5" class="col-md-4 col-lg-3 col-form-label">Nomor Telepon</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.nomor_telepon" type="text" class="form-control" id="input5" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input6" class="col-md-4 col-lg-3 col-form-label">Email</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.email" type="text" class="form-control" id="input6" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="input7" class="col-md-4 col-lg-3 col-form-label">Website</label>
                      <div class="col-md-8 col-lg-9">
                        <input v-model="formData.website" type="text" class="form-control" id="input7" required />
                      </div>
                    </div>
                    <div class="row mb-3">
                      <label for="alamat" class="col-md-4 col-lg-3 col-form-label">Alamat</label>
                      <div class="col-md-8 col-lg-9">
                        <textarea v-model="formData.alamat" class="form-control" id="alamat" style="height: 100px"></textarea>
                      </div>
                    </div>
                    <div class="text-danger fst-italic text-center my-4">{{ errorRequired }}</div>
                    <div class="text-end mx-auto">
                      <button type="button" @click="simpanProfil(formData.id)" class="btn btn-primary mx-auto w-75">Simpan</button>
                    </div>
                  </form>
                  <!-- End Profile Edit Form -->
                </div>
                <div class="tab-pane fade pt-3" id="pengaturan-lokasi">
                  <!-- Settings Form -->
                  <form>
                    <div class="row mb-3">
                      <div class="col-md-8 col-lg-9 px-4">
                        <div class="form-check form-switch mx-2">
                          <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" v-model="dataProfilById.lokasi" @change="toggleLokasi(dataProfilById.id)" />
                          <span class="mx-2">{{ dataProfilById.lokasi ? 'Penggunaan Lokasi AKtif' : 'Penggunaan Lokasi Tidak Aktif' }}</span>
                        </div>
                      </div>
                    </div>
                  </form>
                  <!-- End settings Form -->
                </div>
                <div class="tab-pane fade" id="produk">
                  <table class="table table-sm table-hover" style="font-size: 14px">
                    <thead class="bg-light">
                      <tr>
                        <th scope="col" :class="class_th()" :style="style_th()" style="min-height: 1000px">No.</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Nama Produk</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in dataProduk" :key="item.id">
                        <td :class="class_td()" :style="style_td(index + 1)">{{ index + 1 }}</td>
                        <td class="align-middle" :style="style_td(index + 1)">{{ item.nama }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">
                          <button class="btn btn-danger btn-md mx-1" @click="deleteProduk(item.id)">
                            <i class="bi bi-x-octagon"></i>
                          </button>
                          <button class="btn btn-success btn-md mx-1" @click="getProdukById(item.id)" data-bs-toggle="modal" data-bs-target="#disabledAnimationProduk">
                            <i class="bi bi-pencil-square"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <button type="button" class="btn btn-primary" @click="setDataProdukId" data-bs-toggle="modal" data-bs-target="#disabledAnimationProduk">+ Tambah</button>
                  <div class="modal" id="disabledAnimationProduk" tabindex="-1">
                    <div class="modal-dialog">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Form {{ dataProdukById.id ? 'Edit' : 'Tambah' }} Produk</h5>
                          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                          <form class="row g-3">
                            <div class="col-md-12">
                              <label for="input1" class="form-label">Nama Produk</label>
                              <input v-model="dataProdukById.nama" type="text" class="form-control" id="input1" required />
                            </div>
                            <div class="text-danger fst-italic">{{ errorRequired }}</div>
                          </form>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                          <button @click="updateProduk(dataProdukById.id)" type="button" v-if="dataProdukById.id" class="btn btn-primary">Update</button>
                          <button @click="tambahProduk()" type="button" v-if="!dataProdukById.id" class="btn btn-primary">Simpan</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="tab-pane fade" id="jabatan">
                  <table class="table table-sm table-hover" style="font-size: 14px">
                    <thead class="bg-light">
                      <tr class="my-4">
                        <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Nama Jabatan</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in dataJabatan" :key="item.id">
                        <td :class="class_td()" :style="style_td(index + 1)">{{ index + 1 }}</td>
                        <td class="align-middle" :style="style_td(index + 1)">{{ item.nama }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">
                          <button class="btn btn-success btn-md mx-1" @click="getJabatanById(item.id)" data-bs-toggle="modal" data-bs-target="#disabledAnimationJabatan">
                            <i class="bi bi-pencil-square"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <div class="modal" id="disabledAnimationJabatan" tabindex="-1">
                    <div class="modal-dialog">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Form Jabatan</h5>
                          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                          <form class="row g-3">
                            <div class="col-md-12">
                              <label for="input1" class="form-label">Nama Jabatan</label>
                              <input v-model="dataJabatanById.nama" type="text" class="form-control" id="input1" required />
                            </div>
                            <div class="text-danger fst-italic">{{ errorRequired }}</div>
                          </form>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                          <button @click="updateJabatan(dataJabatanById.id)" type="button" class="btn btn-primary">Update</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="tab-pane fade" id="kemampuan">
                  <table class="table table-sm table-hover" style="font-size: 14px">
                    <thead class="bg-light">
                      <tr class="my-4">
                        <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Nama Kemampuan</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in dataKemampuan" :key="item.id">
                        <td :class="class_td()" :style="style_td(index + 1)">{{ index + 1 }}</td>
                        <td class="align-middle" :style="style_td(index + 1)">{{ item.nama }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">
                          <button class="btn btn-success btn-md mx-1" @click="getKemampuanById(item.id)" data-bs-toggle="modal" data-bs-target="#disabledAnimationKemampuan">
                            <i class="bi bi-pencil-square"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <div class="modal" id="disabledAnimationKemampuan" tabindex="-1">
                    <div class="modal-dialog">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Form Kemampuan</h5>
                          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                          <form class="row g-3">
                            <div class="col-md-12">
                              <label for="input1" class="form-label">Nama Kemampuan</label>
                              <input v-model="dataKemampuanById.nama" type="text" class="form-control" id="input1" required />
                            </div>
                            <div class="text-danger fst-italic">{{ errorRequired }}</div>
                          </form>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                          <button @click="updateKemampuan(dataKemampuanById.id)" type="button" class="btn btn-primary">Update</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="tab-pane fade" id="gaji">
                  <table class="table table-sm table-hover" style="font-size: 14px">
                    <thead class="bg-light">
                      <tr class="my-4">
                        <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Nominal Gaji</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Jabatan</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Kemampuan</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in dataGaji" :key="item.id">
                        <td :class="class_td()" :style="style_td(index + 1)">{{ index + 1 }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.nominal }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.jabatan.nama }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.kemampuan.nama }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">
                          <button class="btn btn-success btn-md mx-1" @click="getGajiById(item.id)" data-bs-toggle="modal" data-bs-target="#disabledAnimationGaji">
                            <i class="bi bi-pencil-square"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <div class="modal" id="disabledAnimationGaji" tabindex="-1">
                    <div class="modal-dialog">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Form Gaji</h5>
                          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                          <form class="row g-3">
                            <div class="col-md-12">
                              <label for="input1" class="form-label">Nominal Gaji</label>
                              <input v-model="dataGajiById.nominal" type="text" class="form-control" id="input1" required />
                            </div>
                            <div class="col-md-12">
                              <label for="input2" class="form-label">Jabatan</label>
                              <input disabled v-model="dataGajiById.jabatan" type="text" class="form-control" id="input2" required />
                            </div>
                            <div class="col-md-12">
                              <label for="input3" class="form-label">Kemampuan</label>
                              <input disabled v-model="dataGajiById.kemampuan" type="text" class="form-control" id="input3" required />
                            </div>
                            <div class="text-danger fst-italic">{{ errorRequired }}</div>
                          </form>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                          <button @click="updateGajiById(dataGajiById.id)" type="button" class="btn btn-primary">Update</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="tab-pane fade" id="shift">
                  <table class="table table-sm table-hover" style="font-size: 14px">
                    <thead class="bg-light">
                      <tr class="my-4">
                        <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Shift</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Jam Mulai</th>
                        <th scope="col" :class="class_th()" :style="style_th()">Jam Berakhir</th>
                        <th scope="col" :class="class_th()" :style="style_th()" class="text-center">Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in dataStatis" :key="item.id">
                        <td :class="class_td()" :style="style_td(index + 1)">{{ index + 1 }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.shift }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.jam_start }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">{{ item.jam_end }}</td>
                        <td :class="class_td()" :style="style_td(index + 1)">
                          <button class="btn btn-success btn-md mx-1" @click="getStatisById(item.id)" data-bs-toggle="modal" data-bs-target="#disabledAnimationShift">
                            <i class="bi bi-pencil-square"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <div class="modal" id="disabledAnimationShift" tabindex="-1">
                    <div class="modal-dialog">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Form Shift</h5>
                          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                          <form class="row g-3">
                            <div class="col-md-12">
                              <label for="input1" class="form-label">Shift</label>
                              <input v-model="dataStatisById.shift" type="text" class="form-control" id="input1" required />
                            </div>
                            <div class="col-md-12">
                              <label for="input2" class="form-label">Jam Mulai</label>
                              <input v-model="dataStatisById.jam_start" type="time" class="form-control" id="input2" required />
                            </div>
                            <div class="col-md-12">
                              <label for="input3" class="form-label">Jam Berakhir</label>
                              <input v-model="dataStatisById.jam_end" type="time" class="form-control" id="input3" required />
                            </div>
                            <div class="text-danger fst-italic">{{ errorRequired }}</div>
                          </form>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutup</button>
                          <button @click="updateStatisById(dataStatisById.id)" type="button" class="btn btn-primary">Update</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="tab-pane fade pt-3" id="minEfektifMulai">
                  <form class="row g-3">
                    <div class="col-12 col-md-6">
                      <label for="min-efektif-mulai" class="form-label">Minimal Efektif</label>
                      <input v-model="dataProfilById.min_efektif_start" type="text" class="form-control" id="min-efektif-mulai" required />
                      <div class="text-danger fst-italic">{{ errorRequired }}</div>
                      <button @click="updateMinimalEfektif(dataProfilById.id)" type="button" class="btn btn-primary w-100 my-4">Update</button>
                    </div>
                  </form>
                </div>
                <div class="tab-pane fade pt-3" id="nominalbonus">
                  <form class="row g-3">
                    <div class="col-12 col-md-6">
                      <label for="input1" class="form-label">Nominal Bonus</label>
                      <input v-model="dataProfilById.nominal_bonus" type="text" class="form-control" id="input1" required />
                      <div class="text-danger fst-italic">{{ errorRequired }}</div>
                      <button @click="updateNominalBonus(dataProfilById.id)" type="button" class="btn btn-primary w-100 my-4">Update</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <my-upload
        @crop-success="cropSuccess"
        @crop-upload-success="cropUploadSuccess"
        @crop-upload-fail="cropUploadFail"
        v-model="show"
        :method="method"
        :field="field"
        :width="200"
        :height="200"
        :maxSize="maxSize"
        :url="url"
        :lang-ext="langExt"
        :headers="headers"
        :allow-img-format="allowImgFormat"
      />
    </section>
  </main>
</template>

<script>
import KomponenJudul from '@/components/KomponenJudul.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import myUpload from 'vue-image-crop-upload';
import router from '@/router';
import axios from 'axios';
import { API_KEY } from '../../env';
export default {
  components: {
    KomponenJudul,
    'my-upload': myUpload,
  },
  name: 'pagePengaturanView',
  data() {
    return {
      namaPage: 'Pengaturan',
      dataJabatan: [],
      dataJabatanById: {},
      dataKemampuan: [],
      dataKemampuanById: {},
      dataGaji: [],
      dataGajiById: {},
      dataProfilById: {},
      dataStatis: [],
      dataStatisById: {},
      dataProduk: [],
      dataProdukById: {},
      errorRequired: '',
      formData: {
        id: '',
        logo: '',
        nama_perusahaan: '',
        nama_alias: '',
        jenis_usaha: '',
        profil_perusahaan: '',
        pimpinan: '',
        nomor_telepon: '',
        email: '',
        website: '',
        alamat: '',
      },
      url: '',
      show: false,
      imgDataUrl: '',
    };
  },

  props: {
    searchResults: {
      required: true,
    },
    field: {
      type: String,
      default: 'file',
    },
    ki: {
      type: String,
      default: '0',
    },
    modelValue: {
      type: Boolean,
      default: true,
    },
    headers: {
      type: Object,
      default: () => null,
    },
    width: {
      type: Number,
      default: 200,
    },
    height: {
      type: Number,
      default: 200,
    },
    noRotate: {
      type: Boolean,
      default: true,
    },
    noCircle: {
      type: Boolean,
      default: false,
    },
    noSquare: {
      type: Boolean,
      default: false,
    },
    maxSize: {
      type: Number,
      default: 2048,
    },
    langType: {
      type: String,
      default: 'id',
    },
    langExt: {
      type: Object,
      default: () => ({
        hint: 'Klik atau seret file ke sini untuk mengunggah',
        loading: 'Mengunggah…',
        noSupported: 'Browser tidak didukung, silakan gunakan IE10+ atau browser lainnya',
        success: 'Unggahan berhasil',
        fail: 'Gagal mengunggah',
        preview: 'Pratinjau',
        btn: {
          off: 'Batal',
          close: 'Tutup',
          back: 'Kembali',
          save: 'Simpan',
        },
        error: {
          onlyImg: 'Gunakan format gambar.',
          outOfSize: 'Gambar melebihi batas ukuran: ',
          lowestPx: 'Ukuran gambar terlalu kecil. Setidaknya diharapkan: ',
        },
      }),
    },
    imgBgc: {
      type: String,
      default: '#000000',
    },
    withCredentials: {
      type: Boolean,
      default: false,
    },
    method: {
      type: String,
      default: 'PATCH',
    },
    initialImgUrl: {
      type: String,
      default: '',
    },
    allowImgFormat: {
      type: Array,
      default: () => ['JPG', 'PNG'],
    },
  },

  watch: {
    // searchResults: 'getProfilById',
    formData: {
      handler: 'updateUrl',
      deep: true,
    },
  },

  methods: {
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(value) {
      if (value % 2 == 0) {
        return 'background-color:#d0d2ff';
      } else {
        return 'background-color:#eff1ff';
      }
    },
    class_td() {
      return 'align-middle text-center';
    },

    getGaji() {
      axios
        .get(`${API_KEY}gaji`)
        .then((response) => {
          this.dataGaji = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getGajiById(id) {
      axios
        .get(`${API_KEY}gaji/${id}`)
        .then((response) => {
          this.dataGajiById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateGajiById(id) {
      if (this.dataGajiById.nominal == '') return (this.errorRequired = 'Pastikan data diisi...');
      axios
        .patch(`${API_KEY}gaji/${id}`, {
          nominal: this.dataGajiById.nominal,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getGaji();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    getProduk() {
      axios
        .get(`${API_KEY}produk`)
        .then((response) => {
          this.dataProduk = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getProdukById(id) {
      axios
        .get(`${API_KEY}produk/${id}`)
        .then((response) => {
          this.dataProdukById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    tambahProduk() {
      if (this.dataProdukById.nama == '') return (this.errorRequired = 'Pastikan data diisi...');
      axios
        .post(`${API_KEY}produk`, {
          nama: this.dataProdukById.nama,
        })
        .then((response) => {
          if (response.status === 201) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getProduk();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateProduk(id) {
      if (this.dataProdukById.nama == '') return (this.errorRequired = 'Pastikan data diisi...');

      axios
        .patch(`${API_KEY}produk/${id}`, {
          nama: this.dataProdukById.nama,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getProduk();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    deleteProduk(id) {
      SweetAlertHelper.confirm('Yakin dihapus?', 'Data akan dihapus secara permanen.').then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${API_KEY}produk/${id}`)
            .then((response) => {
              if (response.status === 204) {
                SweetAlertHelper.success('Berhasil!', 'Data telah dihapus.');
                this.getProduk();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    setDataProdukId() {
      this.dataProdukById.id = '';
      this.dataProdukById.nama = '';
    },

    getJabatan() {
      axios
        .get(`${API_KEY}jabatan`)
        .then((response) => {
          this.dataJabatan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getJabatanById(id) {
      axios
        .get(`${API_KEY}jabatan/${id}`)
        .then((response) => {
          this.dataJabatanById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateJabatan(id) {
      if (this.dataJabatanById.nama == '') return (this.errorRequired = 'Pastikan data diisi...');

      axios
        .patch(`${API_KEY}jabatan/${id}`, {
          nama: this.dataJabatanById.nama,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getJabatan();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    getKemampuan() {
      axios
        .get(`${API_KEY}Kemampuan`)
        .then((response) => {
          this.dataKemampuan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getKemampuanById(id) {
      axios
        .get(`${API_KEY}Kemampuan/${id}`)
        .then((response) => {
          this.dataKemampuanById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateKemampuan(id) {
      if (this.dataKemampuanById.nama == '') return (this.errorRequired = 'Pastikan data diisi...');

      axios
        .patch(`${API_KEY}Kemampuan/${id}`, {
          nama: this.dataKemampuanById.nama,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getKemampuan();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    getStatis() {
      axios
        .get(`${API_KEY}statis`)
        .then((response) => {
          this.dataStatis = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getStatisById(id) {
      axios
        .get(`${API_KEY}statis/${id}`)
        .then((response) => {
          this.dataStatisById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateStatisById(id) {
      if (this.dataStatisById.shift == '' || this.dataStatisById.jam_start == '' || this.dataStatisById.jam_end == '' || this.dataStatisById.min_efektif_start == '') return (this.errorRequired = 'Pastikan data diisi...');
      axios
        .patch(`${API_KEY}statis/${id}`, {
          shift: this.dataStatisById.shift,
          jam_start: this.dataStatisById.jam_start,
          jam_end: this.dataStatisById.jam_end,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getStatis();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    getUrl(item) {
      return `${API_KEY}${item}`;
    },
    getProfilById() {
      axios
        .get(`${API_KEY}profil/1`)
        .then((response) => {
          this.dataProfilById = response.data.data;
          this.formData = {
            id: this.dataProfilById.id,
            logo: this.dataProfilById.logo,
            nama_perusahaan: this.dataProfilById.nama_perusahaan,
            nama_alias: this.dataProfilById.nama_alias,
            jenis_usaha: this.dataProfilById.jenis_usaha,
            profil_perusahaan: this.dataProfilById.profil_perusahaan,
            pimpinan: this.dataProfilById.pimpinan,
            nomor_telepon: this.dataProfilById.nomor_telepon,
            email: this.dataProfilById.email,
            website: this.dataProfilById.website,
            alamat: this.dataProfilById.alamat,
          };
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    simpanProfil(id) {
      if (
        !this.formData.id ||
        !this.formData.nama_perusahaan ||
        !this.formData.nama_alias ||
        !this.formData.jenis_usaha ||
        !this.formData.pimpinan ||
        !this.formData.profil_perusahaan ||
        !this.formData.nomor_telepon ||
        !this.formData.email ||
        !this.formData.website ||
        !this.formData.alamat
      ) {
        SweetAlertHelper.error('Gagal!', 'Pastikan data terisi semua.');
        return;
      } else {
        try {
          axios
            .patch(`${API_KEY}profil_update_by_id/${id}`, this.formData)
            .then((response) => {
              if (response.status === 200) {
                SweetAlertHelper.success('Berhasil!', response.data.msg);
                this.getProfilById();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        } catch (error) {
          router.push({ name: 'Eror', params: { msg: error } });
        }
      }
    },
    updateMinimalEfektif(id) {
      if (this.dataProfilById.min_efektif_start == '') return (this.errorRequired = 'Pastikan data diisi...');
      axios
        .patch(`${API_KEY}profil_update_min_efektif_start_by_id/${id}`, {
          min_efektif_start: this.dataProfilById.min_efektif_start,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
          }
        })
        .then(() => {
          this.getProfilById();
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    updateNominalBonus(id) {
      if (this.dataProfilById.nominal_bonus == '') return (this.errorRequired = 'Pastikan data diisi...');
      axios
        .patch(`${API_KEY}profil_update_nominal_bonus_by_id/${id}`, {
          nominal_bonus: this.dataProfilById.nominal_bonus,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
          }
        })
        .then(() => {
          this.getProfilById();
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    toggleLokasi(id) {
      axios
        .patch(`${API_KEY}profil_update_lokasi_by_id/${id}`, {
          lokasi: this.dataProfilById.lokasi,
        })
        .then((response) => {
          if (response.status === 200) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            this.errorRequired = '';
            this.getProfilById();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    toggleShow() {
      this.show = !this.show;
    },
    cropSuccess(imgDataUrl) {
      this.imgDataUrl = imgDataUrl;
    },
    cropUploadSuccess(jsonData) {
      if (jsonData.code === 200) {
        this.show = !this.show;
        SweetAlertHelper.success('Berhasil!', jsonData.msg);
        this.getProfilById();
      }
    },
    cropUploadFail(status) {
      this.show = !this.show;
      SweetAlertHelper.error('Gagal!', status);
      this.getProfilById();
    },
    updateUrl() {
      this.url = `${API_KEY}profil_upload_logo/${this.formData.id}`;
    },
  },

  created() {
    this.updateUrl();
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getJabatan();
      this.getProduk();
      this.getKemampuan();
      this.getGaji();
      this.getStatis();
      this.getProfilById();
    } else {
      router.push('/login');
    }
  },
};
</script>
