<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataProduksi.length > 0">
      <div class="card">
        <div class="card-body">
          <button type="button" class="btn btn-primary mb-2" data-bs-toggle="modal" data-bs-target="#modal-tambah-produksi">+ Tambah</button>
          <komponen-tambah-produksi @get-data-produksi="getDataProduksi" />
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="min-width: 900px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Nama</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Jenis Produk</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Jumlah</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Tangal Produksi</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Kualitas</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.id">
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ index + 1 }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ item.karyawan.nama }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ item.produk.nama }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ item.jumlah }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    {{ getTanggalID(item.tanggal) }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)" :for="item.id" style="max-width: 100px">
                    <select v-if="!item.setujui" v-model="item.kualitas" :id="item.id" class="form-select is-invalid">
                      <option disabled selected>---Pilih Kualitas---</option>
                      <option value="Sangat Baik">Sangat Baik</option>
                      <option value="Baik">Baik</option>
                      <option value="Cukup">Cukup</option>
                      <option value="Buruk">Buruk</option>
                    </select>
                    <input v-else type="text" class="form-control is-valid" disabled :id="index + 1" v-model="item.kualitas" />
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1)">
                    <div class="btn-group" role="group" aria-label="Basic mixed styles example">
                      <button @click="setujui(item.id, item.kualitas)" :disabled="item.setujui" type="button" class="btn btn-primary">Setujui</button>
                      <button @click="deleteProduksiById(item.id)" type="button" class="btn btn-danger"><i class="bi bi-trash3-fill"></i></button>
                      <button @click="getDataProduksiById(item.id)" data-bs-toggle="modal" data-bs-target="#modal-edit-produksi" type="button" class="btn btn-warning"><i class="bi bi-pencil-square"></i></button>
                    </div>
                    <komponen-edit-produksi :data-produksi="dataProduksiById" @get-data-produksi="getDataProduksi" />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataProduksi" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<style>
.merah {
  background-color: rgb(255, 198, 198);
}
td.green {
  background: rgb(167, 255, 176);
}
td.yellow {
  background: rgb(228, 255, 137);
}
select::-ms-expand {
  display: none;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: none; /* Menghapus latar belakang (ikon) yang ditambahkan secara default */
}
</style>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenTambahProduksi from '@/components/KomponenTambahProduksi.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import KomponenEditProduksi from '@/components/KomponenEditProduksi.vue';
import { API_KEY } from '../../env';
import router from '@/router';
import { DateTime } from 'luxon';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';

export default {
  name: 'pageDataProduksi',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
    KomponenTambahProduksi,
    KomponenEditProduksi,
  },
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      dataProduksi: [],
      dataProduksiById: {},
      dataPagination: [],
      namaPage: 'Data Produksi',
    };
  },
  props: {
    searchResults: {
      required: true,
    },
  },
  watch: {
    searchResults: 'getDataProduksi',
  },

  methods: {
    getTanggalID(value) {
      const wibTimezone = 'Asia/Jakarta';
      const currentTime = DateTime.fromISO(value, { zone: wibTimezone }).setLocale('id');
      const formattedTime = currentTime.toFormat('dd MMMM yyyy');

      return formattedTime;
    },
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(value) {
      if (value % 2 == 0) {
        return 'background-color:#d0d2ff';
      } else {
        return 'background-color:#eff1ff';
      }
    },
    class_td() {
      return 'align-middle text-center';
    },
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },
    getDataProduksiById(id) {
      axios
        .get(`${API_KEY}produksi/${id}`)
        .then((response) => {
          this.dataProduksiById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    deleteProduksiById(id) {
      SweetAlertHelper.confirm('Apakah yakin?', 'Data akan dihapus permanen.').then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${API_KEY}produksi_hapus/${id}`)
            .then((response) => {
              if (response.status === 204) {
                SweetAlertHelper.success('Berhasil!', 'Data telah dihapus.');
                this.getDataProduksi();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
    getDataProduksi() {
      axios
        .get(`${API_KEY}produksi`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataProduksi = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
    setujui(id, kualitas) {
      this.loading = true;
      const data = {
        kualitas: kualitas,
      };
      axios
        .patch(`${API_KEY}produksi_setujui/${id}`, data)
        .then((response) => {
          if (response.status == 200) {
            SweetAlertHelper.success('Berhasil!', 'Data telah dinilai & disetujui.');
            this.getDataProduksi();
          }
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataProduksi();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
