<template>
  <main id="main" class="main pt-4 mt-4">
    <komponen-judul class="m-4" :title="namaPage" />
    <section class="section dashboard" v-if="dataRiwayatAbsensi.length > 0">
      <div class="card">
        <div class="card-body">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="min-width: 800px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Tanggal</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Status</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Shift</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Start</th>
                  <th scope="col" :class="class_th()" :style="style_th()">End</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Lokasi</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Exp</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.id">
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ index + 1 }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ convertLokal(item.tanggal) }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.hadir }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.shift }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.time_start }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.time_end ? item.time_end : '-' }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.nama_lokasi ? item.nama_lokasi : '-' }}
                  </td>
                  <td :style="style_td(item.hadir)" :class="class_td()">
                    {{ item.exp == true ? 'expried' : 'valid' }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="my-2">
          <komponen-pagination :data="dataRiwayatAbsensi" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import { API_KEY } from '../../env';
import router from '@/router';
import KomponenNotFound from '@/components/KomponenNotFound.vue';

export default {
  name: 'pageRiwayatAbsensi',
  components: {
    KomponenJudul,
    KomponenPagination,
    KomponenNotFound,
  },
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      namaPage: 'Riwayat Absensi',
      id_kryn: '',
      dataRiwayatAbsensi: [],
      dataPagination: [],
      dataMe: {},
    };
  },

  props: {
    searchResults: {
      required: true,
    },
  },

  watch: {
    searchResults: 'getRiwayatAbsensi',
    dataMe: 'getRiwayatAbsensi',
  },

  methods: {
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(hadirStatus) {
      if (hadirStatus === 'Tidak Hadir') {
        return {
          backgroundColor: 'rgb(255, 198, 198)',
          color: 'red',
        };
      } else if (hadirStatus === 'Izin') {
        return {
          backgroundColor: 'rgb(167, 255, 176)',
          color: 'green',
        };
      } else {
        return {};
      }
    },
    class_td() {
      return 'align-middle text-center';
    },
    convertLokal(tgl) {
      const dateObject = new Date(tgl);
      return dateObject.toLocaleDateString();
    },

    getRiwayatAbsensi() {
      axios
        .get(`${API_KEY}absensi_get_by_user/${this.dataMe.uuid}`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataRiwayatAbsensi = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },

    getMe() {
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}me`, {
          headers: {
            'x-access-token': token,
          },
        })
        .then((response) => {
          this.dataMe = response.data.data;
        })
        .then(() => {
          this.getRiwayatAbsensi;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },

    getStyle(hadirStatus) {
      if (hadirStatus === 'Tidak Hadir') {
        return {
          backgroundColor: 'rgb(255, 198, 198)',
          color: 'red',
        };
      } else if (hadirStatus === 'Izin') {
        return {
          backgroundColor: 'rgb(167, 255, 176)',
          color: 'green',
        };
      } else {
        return {};
      }
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.dataSlice();
      this.getMe();
    } else {
      router.push('/login');
    }
  },
};
</script>
