<template>
  <div class="modal fade" id="modal-tambah-karyawan" tabindex="-1">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <h4 class="d-block w-full text-center py-4 mb-0">Tambah Karyawan</h4>
        <button type="button" class="btn-close position-absolute top-0 end-0 p-3 bg-light m-2" data-bs-dismiss="modal" aria-label="Close"></button>
        <div class="modal-body">
          <form class="row g-3 ps-4 pe-4">
            <div class="col-md-12">
              <label for="nama" class="form-label">Nama</label>
              <input v-model="formData.nama" type="text" class="form-control" id="nama" required />
            </div>
            <div class="col-md-12">
              <label for="t-email" class="form-label">Email</label>
              <input v-model="formData.email" type="text" class="form-control" id="t-email" @input="validateEmail" required />
              <div v-if="errResponse.err_email" class="text-danger fst-italic text-start">{{ errResponse.err_email }}</div>
            </div>
            <div class="col-md-12">
              <label for="Password" class="form-label">Password</label>
              <input v-model="formData.password" type="text" class="form-control" id="Password" @input="validatePassword" required />
              <div v-if="errResponse.err_password" class="text-danger fst-italic text-start">{{ errResponse.err_password }}</div>
            </div>
            <div class="col-md-12">
              <label for="confirm" class="form-label">Confirm Password</label>
              <input v-model="formData.confirm_password" type="text" class="form-control" id="confirm" required />
              <div class="text-danger fst-italic text-start">{{ errResponse.err_passwordConfirm }}</div>
            </div>
            <div class="col-md-12">
              <label for="nik" class="form-label">NIK</label>
              <input v-model="formData.no_nik" type="text" class="form-control" id="nik" @input="validateNoNIK" />
              <div v-if="errResponse.err_no_nik" class="text-danger fst-italic text-start">{{ errResponse.err_no_nik }}</div>
            </div>
            <div class="col-md-12">
              <label for="lk" class="form-label">Jenis Kelamin</label>
              <div class="d-flex">
                <div class="form-check">
                  <input class="form-check-input" type="radio" v-model="formData.jk" id="lk" value="Laki-laki" checked />
                  <label class="form-check-label" for="lk"> Laki-laki </label>
                </div>
                <div class="form-check ms-4">
                  <input class="form-check-input" type="radio" v-model="formData.jk" id="pr" value="Perempuan" />
                  <label class="form-check-label" for="pr"> Perempuan </label>
                </div>
              </div>
            </div>
            <div class="col-md-12">
              <label for="jabatan" class="form-label">Jabatan</label>
              <select v-model="formData.id_jabatan" class="form-select" id="jabatan">
                <option selected>---Pilih Jabatan</option>
                <option v-for="jbt in jabatan" :key="jbt.id" :value="jbt.id">{{ jbt.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="tmplahir" class="form-label">Tempat lahir</label>
              <select v-model="formData.tmp_lhr" class="form-select" id="tmplahir">
                <option selected>---Pilih Tempat Lahir---</option>
                <option v-for="kab in kota" :key="kab.id" :value="kab.id">{{ kab.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="tgllahir" class="pb-2">Tanggal Lahir</label>
              <input v-model="formData.tgl_lhr" type="date" class="form-control" id="tgllahir" />
            </div>
            <div class="col-md-12">
              <label for="domisili" class="form-label">Domisili</label>
              <select v-model="formData.id_kab" class="form-select" id="domisili">
                <option selected>---Pilih Domisili---</option>
                <option v-for="kab in kota" :key="kab.id" :value="kab.id">{{ kab.nama }}</option>
              </select>
            </div>
            <div class="col-md-12">
              <label for="no-telp" class="form-label">Nomor Telepon</label>
              <input v-model="formData.no_telepon" type="text" class="form-control" id="no-telp" @input="validateNoTelepon" />
              <span v-if="errResponse.err_no_telepon" class="error-message text-danger fst-italic">{{ errResponse.err_no_telepon }}</span>
            </div>
            <div class="col-md-12">
              <label for="no-rek" class="form-label">Nomor Rekening</label>
              <input v-model="formData.no_rekening" type="text" class="form-control" id="no-rek" @input="validateNoRekening" />
              <span v-if="errResponse.err_rekening" class="error-message text-danger fst-italic">{{ errResponse.err_rekening }}</span>
            </div>
            <div class="col-md-12">
              <label for="skil" class="form-label">Tingkat Skill</label>
              <select v-model="formData.id_kemampuan" class="form-select" id="skil">
                <option selected>---Pilih Skill---</option>
                <option v-for="skl in skill" :key="skl.id" :value="skl.id">{{ skl.nama }}</option>
              </select>
            </div>
          </form>
        </div>
        <div class="text-danger fst-italic text-center">{{ errResponse.err_required }}</div>
        <div class="modal-footer">
          <button type="button" @click="clearForm" class="btn btn-danger">Bersih</button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Tutp</button>
          <button type="button" @click="simpanData" class="btn btn-primary" data-bs-dismiss="">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';

export default {
  name: 'KomponenTambahKaryawan',
  data() {
    return {
      formData: {
        nama: '',
        email: '',
        password: '',
        confirm_password: '',
        no_nik: '',
        jk: 'Laki-laki',
        id_jabatan: '',
        tmp_lhr: '3511',
        tgl_lhr: '2000-10-12',
        id_kab: '3511',
        no_telepon: '',
        no_rekening: '',
        id_kemampuan: '',
      },
      errResponse: {
        err_required: '',
        err_email: '',
        err_no_telepon: '',
        err_no_nik: '',
        err_passwordConfirm: '',
        err_password: '',
        err_rekening: '',
      },
      kota: [],
      jabatan: [],
      skill: [],
    };
  },
  methods: {
    async getKota() {
      const response = await axios.get(`${API_KEY}kabupaten`);
      this.kota = response.data.data;
    },
    async getSkill() {
      const response = await axios.get(`${API_KEY}kemampuan`);
      this.skill = response.data.data;
    },
    async getJabatan() {
      const response = await axios.get(`${API_KEY}jabatan`);
      this.jabatan = response.data.data;
    },

    refresKaryawan() {
      this.$emit('get-data-karyawan');
    },

    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.formData.email)) {
        this.errResponse.err_email = 'Format email tidak valid.';
      } else {
        this.errResponse.err_email = '';
      }
    },

    validatePassword() {
      const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

      if (!passwordPattern.test(this.formData.password)) {
        this.errResponse.err_password = 'Password harus terdiri dari setidaknya 8 karakter, minimal 1 huruf kapital, 1 huruf kecil, 1 angka, dan 1 karakter khusus.';
      } else {
        this.errResponse.err_password = '';
      }
    },

    validateNoTelepon() {
      const teleponPattern = /^\d{11,13}$/;
      if (!teleponPattern.test(this.formData.no_telepon)) {
        this.errResponse.err_no_telepon = 'Nomor telepon harus terdiri dari 11 hingga 13 digit angka.';
      } else {
        this.errResponse.err_no_telepon = '';
      }
    },

    validateNoNIK() {
      const teleponPattern = /^\d{16,16}$/;
      if (!teleponPattern.test(this.formData.no_nik)) {
        this.errResponse.err_no_nik = 'Nomor NIK harus terdiri dari 16 digit angka.';
      } else {
        this.errResponse.err_no_nik = '';
      }
    },
    validateNoRekening() {
      const teleponPattern = /^\d{09,16}$/;
      if (!teleponPattern.test(this.formData.no_rekening)) {
        this.errResponse.err_rekening = 'Nomor rekening harus terdiri dari 9 sampai 16 digit angka.';
      } else {
        this.errResponse.err_rekening = '';
      }
    },

    async simpanData() {
      if (
        !this.formData.nama ||
        !this.formData.email ||
        !this.formData.password ||
        !this.formData.confirm_password ||
        !this.formData.no_nik ||
        !this.formData.jk ||
        !this.formData.id_jabatan ||
        !this.formData.tmp_lhr ||
        !this.formData.tgl_lhr ||
        !this.formData.id_kab ||
        !this.formData.no_telepon ||
        !this.formData.no_rekening ||
        !this.formData.id_kemampuan
      ) {
        this.errResponse = {
          err_required: '',
          err_email: '',
          err_no_telepon: '',
          err_no_nik: '',
          err_passwordConfirm: '',
          err_password: '',
          err_rekening: '',
        };
        this.errResponse.err_required = 'Pastikan data diisi semua!';
      } else {
        this.errResponse = {
          err_required: '',
          err_email: '',
          err_no_telepon: '',
          err_no_nik: '',
          err_passwordConfirm: '',
          err_password: '',
          err_rekening: '',
        };
        try {
          const response = await axios.post(`${API_KEY}karyawan_tambah/`, this.formData);
          if (response.status === 201) {
            SweetAlertHelper.success('Berhasil!', response.data.msg);
            router.push({ name: 'pageKaryawanAktif', params: { msg: response.data.msg } });
            this.clearForm();
            this.refresKaryawan();
          }
        } catch (error) {
          if (error.response.status == 422) {
            this.errResponse.err_passwordConfirm = error.response.data.msg;
          } else if (error.response.status == 409) {
            this.errResponse.err_email = error.response.data.msg;
          } else {
            router.push({ name: 'Eror', params: { msg: error } });
          }
        }
      }
    },
    clearForm() {
      (this.formData = {
        nama: '',
        email: '',
        password: '',
        confirm_password: '',
        no_nik: '',
        jk: 'Laki-laki',
        id_jabatan: '',
        tmp_lhr: '',
        tgl_lhr: '',
        id_kab: '',
        no_telepon: '',
        no_rekening: '',
        id_kemampuan: '',
      }),
        (this.errResponse = {
          err_required: '',
          err_email: '',
          err_no_telepon: '',
          err_no_nik: '',
          err_passwordConfirm: '',
          err_password: '',
          err_rekening: '',
        });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getKota();
      this.getJabatan();
      this.getSkill();
    } else {
      router.push('/login');
    }
  },
};
</script>
