<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataKaryawan.length > 0">
      <div class="card">
        <div class="d-flex w-50 px-3 py-4 overflow-auto" style="min-width: 300px">
          <select v-model="selectedYear" class="form-select mx-2" id="ss">
            <option v-for="year in years" :key="year" :value="year">{{ year }}</option>
          </select>
          <select v-model="selectedMonth" class="form-select mx-2" id="dd">
            <option v-for="month in months" :key="month.value" :value="month.value">{{ month.name }}</option>
          </select>
        </div>
        <div class="card-body py-2">
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-bordered table-hover" style="font-size: 11px; min-width: 2000px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()" rowspan="2">NO.</th>
                  <th scope="col" :class="class_th()" :style="style_th()" rowspan="2">NOMOR NIK</th>
                  <th scope="col" :class="class_th()" :style="style_th()" rowspan="2">NAMA</th>
                  <th scope="col" :class="class_th()" :style="style_th()" rowspan="2">JENIS KELAMIN</th>
                  <th scope="col" :class="class_th()" :style="style_th()" rowspan="2">JABATAN</th>
                  <th scope="col" :class="class_th()" :style="style_th()">DURASI NORMAL</th>
                  <th scope="col" :class="class_th()" :style="style_th()">DURASI LEMBUR</th>
                  <th scope="col" :class="class_th()" :style="style_th()">HADIR</th>
                  <th scope="col" :class="class_th()" :style="style_th()">ABSEN</th>
                  <th scope="col" :class="class_th()" :style="style_th()">EFEKTIF</th>
                  <th scope="col" :class="class_th()" :style="style_th()">NORMAL</th>
                  <th scope="col" :class="class_th()" :style="style_th()">LEMBUR</th>
                  <th scope="col" :class="class_th()" :style="style_th()">NOMINAL GAJI / JAM</th>
                  <th scope="col" :class="class_th()" :style="style_th()">NOMINAL BONUS / HARI</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GAJI NORMAL</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GAJI LEMBUR</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GAJI BONUS</th>
                  <th scope="col" :class="class_th()" :style="style_th()">TOTAL GAJI</th>
                </tr>
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()">DN</th>
                  <th scope="col" :class="class_th()" :style="style_th()">DL</th>
                  <th scope="col" :class="class_th()" :style="style_th()">H</th>
                  <th scope="col" :class="class_th()" :style="style_th()">A</th>
                  <th scope="col" :class="class_th()" :style="style_th()">N</th>
                  <th scope="col" :class="class_th()" :style="style_th()">L</th>
                  <th scope="col" :class="class_th()" :style="style_th()">E</th>
                  <th scope="col" :class="class_th()" :style="style_th()">NG</th>
                  <th scope="col" :class="class_th()" :style="style_th()">NB</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GN = NG x DN</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GL = (2 x NG x DN) - (1.5 x NG)</th>
                  <th scope="col" :class="class_th()" :style="style_th()">GB = NB x E</th>
                  <th scope="col" :class="class_th()" :style="style_th()">TG = GN + GL + SGB</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.uuid">
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ index + 1 }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.no_nik }}
                  </td>
                  <td class="align-middle" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.nama }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.jk }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.jabatan.nama }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.total_durasi_normal ? item.total_durasi_normal.replace('-', '') : '00:00:00' }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ item.total_durasi_lembur ? item.total_durasi_lembur.replace('-', '') : '00:00:00' }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.hadir }} Hari</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.tidak }} Hari</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.bonus }} Hari</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.normal }} Hari</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.lembur }} Hari</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.nominal_gaji_per_jam ? formatRupiah(parseFloat(item.nominal_gaji_per_jam)) : 'Rp 0' }}</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ nominalBonusPerHari(item.gaji_bonus, jumlahHariEfektif) ? formatRupiah(nominalBonusPerHari(item.gaji_bonus, jumlahHariEfektif)) : 'Rp 0' }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ kalkulasiBonus(item.gaji_bonus, jumlahHariEfektif, item.bonus) ? formatRupiah(kalkulasiBonus(item.gaji_bonus, jumlahHariEfektif, item.bonus)) : 'Rp 0' }}
                  </td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.gaji_normal ? formatRupiah(parseFloat(item.gaji_normal)) : 'Rp 0' }}</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">{{ item.gaji_lembur ? formatRupiah(parseFloat(item.gaji_lembur)) : 'Rp 0' }}</td>
                  <td :class="class_td()" :style="{ 'background-color': style_td(index + 1) }">
                    {{ formatRupiah((item.gaji_normal ? parseFloat(item.gaji_normal) : 0) + (item.gaji_lembur ? parseFloat(item.gaji_lembur) : 0) + (item.gaji_bonus ? parseFloat(item.gaji_bonus) : 0)) }}
                  </td>
                </tr>
              </tbody>
            </table>
            <!-- End small tabitemles -->
          </div>
        </div>

        <button type="button" class="btn btn-primary d-none" ref="modalButton" data-bs-toggle="modal" data-bs-target="#fullscreenModal">Full Screen Modal</button>
        <page-export-gaji :dataLaporanGaji="dataKaryawan" :bulan="selectedMonthName" :tahun="selectedYear" :numberMonth="selectedMonth" />
        <div class="my-2">
          <komponen-pagination :data="dataKaryawan" @update:data="dataSlice" />
        </div>
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import router from '@/router';
import PageExportGaji from '../components/exsport/export_gaji.vue';
import KomponenJudul from '@/components/KomponenJudul.vue';
import KomponenNotFound from '@/components/KomponenNotFound.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import { API_KEY } from '../../env';

export default {
  name: 'pageLapAbsensi',
  components: {
    PageExportGaji,
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
  },
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
      index: 0,
      namaPage: 'Laporan Gaji',
      id_kryn: '',
      document: {},
      selectedYear: new Date().getFullYear(),
      selectedMonth: new Date().getMonth() + 1,
      dates: [],
      years: [],
      months: [
        { value: 1, name: 'Januari' },
        { value: 2, name: 'Februari' },
        { value: 3, name: 'Maret' },
        { value: 4, name: 'April' },
        { value: 5, name: 'Mei' },
        { value: 6, name: 'Jui' },
        { value: 7, name: 'Juli' },
        { value: 8, name: 'Augustus' },
        { value: 9, name: 'September' },
        { value: 10, name: 'Oktober' },
        { value: 11, name: 'November' },
        { value: 12, name: 'Desember' },
      ],
      dataKaryawan: [],
      dataPagination: [],
      laporanGaji: [],
    };
  },

  props: {
    searchResults: {
      required: true,
    },
    exports_status: {
      required: true,
    },
  },

  methods: {
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    style_td(value) {
      if (value % 2 == 0) {
        return '#d0d2ff';
      } else {
        return '#eff1ff';
      }
    },
    class_td() {
      return 'align-middle text-center';
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },

    getDataKaryawan() {
      const token = localStorage.getItem('accessToken');
      axios
        .get(`${API_KEY}laporan_gaji`, {
          headers: {
            'x-access-token': token,
          },
          params: {
            search_query: this.searchResults,
            year: this.selectedYear,
            month: this.selectedMonth,
          },
        })
        .then((response) => {
          this.dataKaryawan = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },

    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },

    kalkulasiBonus(nominalBonus, jumlahEfektif, jumlahBonus) {
      const bon = parseFloat(nominalBonus);
      const efk = parseFloat(jumlahEfektif);
      return (bon / efk) * jumlahBonus;
    },
    nominalBonusPerHari(nominalBonus, jumlahEfektif) {
      const bon = parseFloat(nominalBonus);
      const efk = parseFloat(jumlahEfektif);
      return bon / efk;
    },

    formatRupiah(value) {
      if (typeof value !== 'number') {
        return value;
      }
      const formatter = new Intl.NumberFormat('id-ID', {
        style: 'currency',
        currency: 'IDR',
        minimumFractionDigits: 0,
        maximumFractionDigits: 0,
      });
      return formatter.format(value).replace('-', '');
    },

    async statusExp() {
      const btn = this.$refs.modalButton;
      if (this.exports_status === true) {
        btn.click();
        this.getDataKaryawan();
      }
    },
    getYears() {
      const currentYear = new Date().getFullYear();
      for (let i = currentYear; i >= currentYear - 10; i--) {
        this.years.push(i);
      }
    },
  },

  computed: {
    jumlahHariEfektif() {
      let daysInMonth = new Date(this.selectedYear, this.selectedMonth, 0).getDate();
      let count = 0;
      for (let day = 1; day <= daysInMonth; day++) {
        let date = new Date(this.selectedYear, this.selectedMonth - 1, day);
        let dayOfWeek = date.getDay();
        if (dayOfWeek !== 0 && dayOfWeek !== 6) {
          count++;
        }
      }
      return count;
    },
    selectedMonthName() {
      const selectedMonthObject = this.months.find((month) => month.value === this.selectedMonth);
      return selectedMonthObject ? selectedMonthObject.name : 'Not Found';
    },
  },

  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataKaryawan();
      this.dataSlice();
      this.getYears();
    } else {
      router.push('/login');
    }
  },

  watch: {
    selectedYear: 'getDataKaryawan',
    selectedMonth: 'getDataKaryawan',
    searchResults: 'getDataKaryawan',
    exports_status: 'statusExp',
  },
};
</script>
